<template>

    <div style=" display: contents; float: left;width: 100%;">
        <template v-for="item in store.getTimerListingFromTrackId">
            <tr style="background: #def1ff!important;" v-for="(data, i) in item.entryList" :key="i"
                :trackId="data.trackId">
                <td class="tabchild">
                    <div class="form-check form-check-inline">
                        <!-- <div class="custom-control custom-checkbox">
                                                            <input type="checkbox" :class="{'chkItems custom-control-input':true}"  :value="data.trackId" name="DeleteInputs" 
                                                            :id="'chk_'+data.trackId" v-model="store.$state.selectedValues" style="display: none;" >
                                                            <label class="custom-control-label universal-custom-control-label pt-1" :for="'chk_'+data.trackId"></label>
                                                        </div> -->

                    </div>

                </td>
                <td>
                    <!-- <a id="aParentID" :class="{ 'parentwithchild newtimetrack d-inline-block parent-childrow expandchild': data.trackLinkedId > 0, 'd-none': data.trackLinkedId <= 0 }" href="javascript:;" ><i class="fa fa-chevron-down" 
                                                        ></i></a> -->
                    <div class="d-inline-block mt-1" v-if="data.trackType != 'Break'">
                        <a title="" value="-1" style="cursor:default;" class="text-dark">
                            <span class="d-block font-14 font-weight-medium">{{ data.projectName }} :{{ data.taskName
                                }}</span>
                            <span class="text-dark d-block font-12">{{ data.trackDescription }}</span> </a>
                    </div>
                    <div class="d-inline-block mt-2" v-else style="max-width: 30ch;
                                                                    white-space: nowrap;
                                                                        overflow: hidden;
                                                                text-overflow: ellipsis;" :title="data.trackDescription">
                                        
                                                                <span class="d-block font-14 font-weight-medium">{{
                                                                    data.trackDescription }} </span>
                                                            </div>
                    <a class="float-right ml-3 mr-3 mt-2" title="" data-toggle-tooltip="tooltip"
                        data-original-title="Click here to view track Location(s)">
                        <em class="pl-2 pt-2  text-secondary" :class="CSStrackBy(data.trackAgent)"
                            style="font-size:22px;cursor:pointer;"></em>
                    </a>

                </td>

                <td>
                    <div class="dottedbod_rt w-100 row mt-2">
                        <div class="float-left tagspanel">
                            <i
                                :class="{ 'tt_lisitng_icons ico_listtags float-left': (data.tag_name != null), 'tt_lisitng_icons ico_listtags-o float-left': (data.tag_name == null) }"></i>
                            <span v-for="(tags, tagIndex) of arrayConvert(data.tag_name)" :key="tagIndex"
                                :class="tagIndex > 1 || showAllTags ? 'd-none' : ''">{{ tags }}</span>

                            <!-- Display "2 More" link only if there are more than two tags -->
                            <a v-if="data.tag_name != null && !showAllTags" @click="showAllTags = true" class=""
                                href="#">2 {{ $t('More') }}</a>

                            <!-- Display all spans if the user has clicked the "2 More" link -->
                            <span v-for="(tags, tagIndex) of arrayConvert(data.tag_name)" :key="tagIndex"
                                v-show="showAllTags">{{ tags }}</span>

                        </div>
                        <div class="float-left">
                            <i
                                :class="{ 'tt_lisitng_icons ico_listdollar float-left': (data.billableMark == '$'), 'tt_lisitng_icons ico_listdollar-o float-left': (data.billableMark != '$') }"></i>
                        </div>
                        <div class="float-left" v-if='data.trackType == "Break"'>

                            <em class="tt_lisitng_icons ico_listcup1 float-left"></em>
                        </div>
                    </div>
                </td>

                <td>
                    <span class="font-14 font-weight-medium d-flex pt-3">
                        <i class="tt_lisitng_icons ico_listclock float-left"></i> {{ data.startTime }} - {{
            data.stopTime }}
                    </span>
                </td>
                <td class="timetracked"><span class="mt-1">{{ data.totalHours }}</span></td>
                <td>

                </td>
                <!-- <td class="text-center mobile-action">
                                                            <span class="actions mobileaction">
                                                                <em aria-hidden="true"></em>
                                                                <em class="fa fa-ellipsis-h action_icon" aria-hidden="true"
                                                                    @click="ToggleActionButton(data.trackId)"></em>
                                                                <span class="action-list-box" style="overflow: visible;"
                                                                    v-bind:style="GetIndex === data.trackId ? 'width: 393px; display: block; opacity: 1;' : 'display: none; opacity: 0;'">

                                                                    <span class="list-actions" id="action-list">
                                                                         <a class=" actions-onclick" data-toggle="modal"  @click="duplicate(data,i)"
                                                                            href="javascript:;">
                                                                            <em
                                                                                class="fa fa-copy mr-1 text-primary"
                                                                               ></em><span
                                                                                class="text">Duplicate</span>
                                                                        </a> 
                                                                        <a class=" actions-onclick" data-toggle="modal"
                                                                            @click="deleteChild(data.trackId)">
                                                                            <em
                                                                                class="fa fa-trash mr-1 text-primary"></em><span
                                                                                class="text">{{$t('Delete')}}</span>
                                                                        </a>
                                                                        <em class="fa fa-times close close-action"
                                                                            aria-hidden="true"
                                                                            @click="ToggleActionButton(GetIndex)"></em>
                                                                    </span> </span>
                                                            </span>
                                                        </td> -->
                <td class="text-center mobile-action">
                    <span class="actions mobileaction">
                        <em aria-hidden="true"></em>

                        <em class="fa fa-ellipsis-h action_icon" aria-hidden="true"
                            @click="ToggleActionButton(data.trackId)"></em>
                        <span class="action-list-box" style="overflow: visible;"
                            v-bind:style="GetIndex === data.trackId ? 'width: 393px; display: block; opacity: 1;' : 'display: none; opacity: 0;'">

                            <span class="list-actions p-0" id="action-list">

                                <a class=" actions-onclick" data-toggle="modal" @click="deleteChild(data.trackId)"
                                    href="javascript:;">
                                    <!-- <em class="fa fa-trash mr-1 text-primary"></em> -->
                                    <span class="text"> {{ $t('Delete') }}</span>
                                </a>
                                <!-- <em class="fa fa-times close close-action" aria-hidden="true" @click="ToggleActionButton(GetIndex)"></em> -->
                            </span> </span>
                    </span>
                </td>
            </tr>

        </template>
    </div>
    <!-- <tr v-for="(data,i) in item.entryList" :key="i"><td>{{ data }}</td></tr> -->

</template>

<script>
import { defineComponent } from '@vue/composition-api'
import { userTimerInfo } from "../../stores/TimeTrack.js"
import DuplicateEntry from './modals/DuplicateEntry.vue';
import { storeToRefs } from 'pinia';
import i18n from '../../localization/index'
export default defineComponent({
    components: { DuplicateEntry },
    setup() {
        const store = userTimerInfo();
        const { getTimerListing, deleteChildTimer } = storeToRefs(store)
        return {
            store,
            getTimerListing,
            deleteChildTimer
        }
    },
    props: {
        list: {
            type: Array
        }
    },
    data() {
        return {
            showAllTags: false,
            selectedCheckboxes: [],
            isActiveActionButton: false,
            GetIndex: null,
            duplicateShow: false,
            duplicateEntry: ''
        }
    },
    created() {
        console.log("checkkk", this.list)
    },
    methods: {
        arrayConvert(event) {
            if (event) {
                let convert = event.split(',')
                return convert
            }
            else {
                return []
            }
        },
        ToggleActionButton(index) {
            var vm = this;
            this.isActiveActionButton = !this.isActiveActionButton;
            if (index == this.GetIndex) {
                this.GetIndex = ''; // Set the index if needed
            } else {
                this.GetIndex = index
            }
        },
        async deleteChild(id) {
            var vm = this
            vm.confirmR(i18n.t("ConfirmDelete"), true, (i18n.t("Delete") + "  " + i18n.t("Entry")), async function () {
                var params = `ids=${id}&childId=1`;
                await vm.store.deleteChildTimer(params)
                //vm.store.$state.timerListing = []
                vm.$emit('closeCollapsed')
                const url = `dateType=${vm.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                vm.store.getTimerListing1(url)
                const url1 = `dateType=${vm.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                vm.store.getTrackedtimings(url1)

            });
        },
        async duplicate(obj, index) {
            var vm = this
            vm.confirmR(i18n.t("ConfirmDuplicateEntry"), true, (i18n.t("Delete") + "  " + i18n.t("entry")), function () {
                console.log("check", obj)
                vm.$parent.duplicateEntry = obj
                //console.log("trackLinkedId",obj.trackLinkedId)
                vm.$parent.duplicateShow = true
                // vm.duplicateShow = vm.duplicateShow =!vm.duplicateShow
            })
        },
        CSStrackBy(data) {

            let trackBy = "";
            switch (data.toLowerCase()) {
                case "desktop":
                case "linux":
                    trackBy = "fa fa-desktop font-23";
                    break;
                case "Web":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "web":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "web_manual":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "timesheet":
                    trackBy = "fa fa-globe font-23";
                    break;
                // Add other cases as needed
                case "addedmanualpending":
                    trackBy = "fa fa-globe font-23";
                    break;
                default:
                    trackBy = "fa fa-mobile font-30";
                    break;
            }
            return trackBy;
        },
    },

})
</script>
<style scoped>
.action-list-box {
    opacity: 0;
    height: 0px;
    transition: opacity 1s;
}

.is-visible {
    display: inline-block;
    height: auto;
    overflow: hidden !important;
}
</style>