                                                                    @click="updateIsbillable(data.trackId, data.billableMark != '$' ? 'Billable' : '')"
                                                                    style="font-size:22px;cursor:pointer;"></em>
                                                            </div>
                                                            <div class="float-left position-relative"
                                                                v-if='data.trackType == "Break"'>
                                                                <em
                                                                    class="tt_lisitng_icons ico_listcup1 float-left"></em>
