                                                    <div class="timetracked float-left px-4 mt-1">
                                                        <span class="mt-1">{{ data.totalHours }}</span>
                                                    </div>

                                                    <div>


                                                        <div :class="store.getTimer.trackAgent == 'silentapp' ? 'tt_disabled' : ''"
                                                            v-if='(data.projectStatus != "INACT" || data.projectStatus != "DEL") && data.trackType == "TimeTrack" || (data.trackType == "Break" && data.takenBreak > 0 && store.getTimer.enableBreak == "yes")'>
                                                            <div v-if='data.statusCode != "TRACK_ENTRY_APPROVED"'>
                                                                <div class="float-left pt-3" @click="startTimer(data)"
                                                                    v-if='!((data.statusCode == "TRACK_ENTRY_ADD_MANUAL_PENDING" && item.approvalTypeForTime == "Timesheet"))'>
                                                                    <em class="tt_lisitng_icons ico_listplaytime float-left"
                                                                        :title="$t('ContinueEntry')"
                                                                        style="font-size: 22px;cursor:pointer;"
                                                                        :trackId=data.trackId
                                                                        :trackDesc=data.trackDescription
                                                                        :trackBillable=data.isBillable
                                                                        :projectId="data.projectId"
                                                                        :taskId=data.taskId></em>
                                                                </div>
                                                                <div v-else>

                                                                    <a class="continuetimer"><em
                                                                            :title="$t('ContinueEntry')"
                                                                            class="fa fa-clock-o text-light action-icon"
                                                                            style="color: #e0e8f0 !important; font-size:22px;cursor:pointer;"></em></a>
                                                                </div>
                                                            </div>
                                                            <div v-else>

                                                                <a class="continuetimer"><em
                                                                        :title="$t('ContinueEntry')"
                                                                        class="fa fa-clock-o text-dark action-icon"
                                                                        style="font-size:22px;cursor:pointer;"></em></a>
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div class="text-center mobile-action">
                                                        <span class="actions mobileaction">
                                                            <em aria-hidden="true"></em>

                                                            <em ref="openActionPopup"
                                                                :class="'fa fa-ellipsis-h action_icon openActionPopup' + GetIndex"
                                                                aria-hidden="true"
                                                                @click="ToggleActionButton(data.trackId)"></em>
                                                            <span ref="actionModal" v-if="GetIndex === data.trackId"
                                                                class="action-list-box actionModal"
                                                                style="overflow: visible;"
                                                                v-bind:style="GetIndex === data.trackId ? 'width: 393px; display: block; opacity: 1;' : 'display: none; opacity: 0;'">

                                                                <span class="list-actions p-0" id="action-list">
                                                                    <a :class="store.getTimer.trackType != null ? 'tt_disabled' : ''"
                                                                        class=" actions-onclick" data-toggle="modal"
                                                                        @click="duplicate(data, i)" href="javascript:;"
                                                                        v-if="data.trackType != 'Break'">
                                                                        <!-- <em class="fa fa-copy mr-1 text-primary"></em> -->
                                                                        <span class="text">{{ $t('Duplicate') }}
                                                                        </span>
                                                                    </a>
                                                                    <a class=" actions-onclick" data-toggle="modal"
                                                                        @click="UpdateOrDeleteEntries(data.trackId)"
                                                                        href="javascript:;">
                                                                        <!-- <em class="fa fa-trash mr-1 text-primary"></em> -->
                                                                        <span class="text"> {{ $t('Delete')
                                                                            }}</span>
                                                                    </a>
                                                                    <a v-if="data.trackType != 'Break'"
                                                                        class=" actions-onclick" data-toggle="modal"
                                                                        @click="editentry(data)" href="javascript:;">
                                                                        <!-- <em class="fa fa-trash mr-1 text-primary"></em> -->
                                                                        <span class="text"> {{ $t('Edit')
                                                                            }}</span>
                                                                    </a>
                                                                    <!-- <em class="fa fa-times close close-action" aria-hidden="true" @click="ToggleActionButton(GetIndex)"></em> -->
                                                                </span> </span>
                                                        </span>
                                                    </div>
                                                </div>

                                            </div>

                                            <TimeTrackChildMobile
                                                v-if="isExpendCollapsed(data.trackId) && store.$state.expendListRow === true"
                                                :key="data.trackId + 'check'" :list="listData[data.trackId + 'abc']"
                                                v-on:closeCollapsed="expendCollapsedSections = [false]">
                                            </TimeTrackChildMobile>

                                        </div>




                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="w-100 float-right flex-row-reverse mb-2 totalactivity_rt">
                    <div class="float-right tagspanel2">

                        <span class="p-0 w-100">
                            <span>{{ $t('TotalTime') }}: <strong>{{ convertToTimeFormatHHMMSS(item.totalTime)
                                    }}</strong></span>
                            <span class="px-0">|</span>

                            <span>{{ $t('UnpaidTime') }}: <strong>{{ convertToTimeFormatHHMMSS(item.unPaidTime)
                                    }}</strong></span>


                            <span v-if="store.getTimer.enableBreak != 'yes'" class="px-0">|</span>
                            <span v-if="store.getTimer.enableBreak != 'yes'"> {{ $t('PausedTime') }}: <strong>{{
                convertToTimeFormatHHMMSS(item.pausedHours) }}</strong></span>
                        </span>

                        <span class="coloder">{{ $t('PaidTime') }}: <strong>{{ convertToTimeFormatHHMMSS(item.paidTime)
                                }}</strong></span>

                    </div>
                </div>
            </div>
        </div>

        <loader :isVisible="getLoadingStatus"></loader>
        <DuplicateEntry v-bind:manualData="duplicateEntry" v-if="duplicateShow" v-on:close="duplicateShow = false" />
        <editTrackTime v-if="editModal" v-on:close="editModal=false" v-bind:store="store"
            v-bind:editTrackRecords="editTrackRecords"></editTrackTime>
    </section>
</template>

