<script>
import { defineComponent, onMounted, reactive, ref } from '@vue/composition-api'
import { userTimerInfo } from "../../stores/TimeTrack.js"
import { storeToRefs } from 'pinia';
import moment from "moment";
import TimeTrackChildTable from './TimeTrackChildTable.vue';
import TimeTrackChildMobile from './TimeTrackChildMobile.vue';
import {
    getPreviousDay, formatter, dateAndTimeZone,
    dateTimeFromat
} from "@/assets/common/vue-common.js"
import DataService from '../../services/DataService.js'
import DuplicateEntry from './modals/DuplicateEntry.vue';
import i18n from '../../localization/index'
import editTrackTime from '../TimeTrack/modals/EditTimeTrackModal.vue'
export default defineComponent({
    components: { TimeTrackChildTable, DuplicateEntry, editTrackTime, TimeTrackChildMobile },


    setup() {
        const store = userTimerInfo();
        const { getTimerListing, getUserInformation, getTimer, getLoadingStatus, getTimerListingFromTrackId, timerInfo, getTimerRunResponse } = storeToRefs(store)
        onMounted(() => {
            const url = `dateType=${store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
            store.getTimerListing1(url)


        })

        const convertToTimeFormatHHMMSS = (seconds) => {
            if (seconds === 0 || seconds < 0) {
                return "00:00:00";
            } else {
                const hours = Math.floor(seconds / 3600);
                const minutes = Math.floor((seconds % 3600) / 60);
                const remainingSeconds = seconds % 60;

                return formatter(seconds);
            }
        };
        const trackerData = reactive({
            projectId: null,
            trackDescription: "",
            taskId: null,
            isBillable: 0,
            format: 0,
            date: null,
            trackId: 0,
            startTime: dateAndTimeZone(moment(), dateTimeFromat),
            stopTime: dateAndTimeZone(moment(), dateTimeFromat),
            companyId: null,
            currentDate: dateAndTimeZone(moment(), dateTimeFromat),
            trackAgent: "web",
            timerStatus: "start",
            CreatedBy: getUserInformation.value.id,
            createdBy: getUserInformation.value.id,
            linkedTrackId: null,
            totalHours: 0,
            timeformat: 12,
            TagIds: null,
            isForcedUpdate: null,
            type: "TimeTrack",
            BreakDate: null,
            BreakName: null,
            BreakId: 0,
            BreakStartTime: 0,
            Shift_ID: 0,
            SelectedDate: "",
            Break_Shift: "",
            iscoutinue: 0,
            BreakStopTime: dateAndTimeZone(moment(), dateTimeFromat),
            showMoreTagData: [],
            showMoreTagDataMob: []
        })

        //Change date type and get data 
        function getListByDateType(type) {
            this.store.$state.dateFilterType = type
            const url = `dateType=${type}&search=&toDate=&fromDate=&newTrackId=0`
            store.getTimerListing1(url)
            const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=${this.store.$state.To}&fromDate=${this.store.$state.From}`
            this.store.getTrackedtimings(url1)
        }
        //Get data from trackId and append in another table

        return {
            getTimerListing,
            convertToTimeFormatHHMMSS,
            store,
            trackerData,

            getListByDateType,
            getLoadingStatus,
            getTimerListingFromTrackId,
            getPreviousDay,
            moment,
            getUserInformation,
            getTimer,
            timerInfo
        }
    },

    data() {
        return {
            collapsedSections: [false], // Each index represents a section, initialized as not collapsed
            selectedCheckboxes: [],
            expendCollapsedSections: [false],// Expend tr in table
            listData: [],
            arrayData: [],
            showAllTags: false,
            GetIndex: null,
            isActiveActionButton: false,
            tagShow: '',
            timerTags: [],
            showTagsToltip: '',
            duplicateShow: false,
            duplicateEntry: '',
            editTrackRecords: '',
            editModal: false,
            tagMobShow: '',
            showTagsToltipMob: '',
        };
    },
    mounted() {
        this.store.$state.selectedValuesWithTypeofObject = []
        this.store.$state.selectedValues = []
        document.addEventListener("click", this.onClickOutside);
        //This script use for tags and break modal auto off when user click any other actions
        

        this.store.$state.headingInactivityTab = 'ActivitiesDoneByYou'
    },
    beforeDestroy() {
        document.removeEventListener("click", this.onClickOutside);
    },
    methods: {
        async startTimer(obj) {

            if (this.$parent.addManualTimeOn == true) {
                this.$parent.addManualTimeOn = !this.$parent.addManualTimeOn

            }

            var vm = this;

            Object.assign(vm.trackerData, obj)
            vm.trackerData.CreatedBy = vm.getUserInformation.id
            vm.trackerData.createdBy = vm.getUserInformation.id
            vm.trackerData.isBillable = obj.billableMark == '$' ? true : false
            vm.store.$state.loading = true
            vm.trackerData.trackId = 0
            vm.trackerData.totalHours = 0
            vm.trackerData.iscoutinue = 1
            vm.trackerData.trackAgent = 'web'
            vm.trackerData.type = vm.trackerData.trackType
            vm.trackerData.TagIds = obj.tagData
            vm.trackerData.dateStart = dateAndTimeZone(moment(), dateTimeFromat)
            if (vm.trackerData.trackType == 'Break') {
                if (vm.timerInfo.trackType == 'Break') {
                    this.ShowAlert(this.$t('BreakStartedAlready'), "warning", true, this.$t('Alert'));
                } else {
                    vm.trackerData.BreakStartTime = dateAndTimeZone(moment(), dateTimeFromat)
                    vm.trackerData.BreakStopTime = dateAndTimeZone(moment(), dateTimeFromat)
                    vm.trackerData.BreakId = obj.breakId
                    vm.trackerData.BreakName = obj.breakName
                    vm.trackerData.BreakDate = dateAndTimeZone(moment(), dateTimeFromat)

                    this.confirmR(this.$t("ConfirmBreakContinue"), true, (this.$t("Send") + "  " + this.$t("Records")), async function () {
                        await store.submitProjectInfo(vm.trackerData)
                        if (vm.timerInfo.statusCode == "ACT" && vm.getTimerRunResponse.value.status != 1) {
                            vm.timerInfo.stopTime = dateAndTimeZone(moment(), dateTimeFromat)
                            vm.timerInfo.totalHours = vm.store.$state.timerCountInSec
                            vm.timerInfo.timerStatus = 'pause'
                            vm.timerInfo.createdBy = vm.trackerData.CreatedBy
                            vm.timerInfo.type = vm.timerInfo.trackType
                            vm.timerInfo.currentDate = dateAndTimeZone(moment(), dateTimeFromat)
                            await vm.store.submitProjectInfo(vm.timerInfo)
                            vm.store.stopTimer()
                        }
                        vm.$emit('timerClose')
                        if (vm.getTimer) {
                            setTimeout(() => {
                                vm.$emit('timerOpen')
                            }, 300);
                        }
                    })
                }

            } else {
                vm.trackerData.startTime = dateAndTimeZone(moment(), dateTimeFromat)
                vm.trackerData.stopTime = dateAndTimeZone(moment(), dateTimeFromat)
                if (vm.timerInfo.trackType == 'TimeTrack') {
                    this.ShowAlert(this.$t('TimerStarted'), "warning", true, this.$t('Alert'));
                } else {
                    this.confirmR(this.$t("ConfirmTimerContinue"), true, (this.$t("Send") + "  " + this.$t("Records")), async function () {
                        if (vm.timerInfo.statusCode) {
                            vm.timerInfo.stopTime = dateAndTimeZone(moment(), dateTimeFromat)
                            vm.timerInfo.totalHours = vm.store.$state.timerCountInSec
                            vm.timerInfo.timerStatus = 'stop'
                            vm.timerInfo.createdBy = vm.trackerData.CreatedBy
                            vm.timerInfo.value.type = vm.timerInfo.trackType
                            vm.timerInfo.BreakStartTime = dateAndTimeZone(moment(), dateTimeFromat)
                            vm.timerInfo.BreakStopTime = dateAndTimeZone(moment(), dateTimeFromat)
                            vm.timerInfo.currentDate = dateAndTimeZone(moment(), dateTimeFromat)
                            await vm.store.submitProjectInfo(vm.timerInfo)
                            vm.store.breakStopTimer()
                        }
                        vm.store.submitProjectInfo(vm.trackerData)
                        vm.$emit('timerClose')
                        if (vm.getTimer) {
                            setTimeout(() => {
                                vm.$emit('timerOpen')
                            }, 300);
                        }
                    })
                }
            }
            vm.store.$state.loading = false

        },
        tagModalClose() {

            this.tagShow = ''
        },
        tagModalMobClose() {

            this.tagMobShow = ''
        },
        closeTooltip() {
            this.showTagsToltip = ''
        },
        TruncateStr: function (str, len) {
            var convertstring = '' + str;
            if (convertstring != null)
                if (convertstring.length <= len) return str;
                else return convertstring.substring(0, len) + "...";
        },
        closeTooltipmob() {
            this.showTagsToltipMob = ''
        },
        // tagmodalmoblclose(){
        //     this.tagShow = '';
        // },
        onClickOutside(e) {
            // if (this.tagShow > 0) {
            //     var tagsMultiselect = document.querySelector('.tagsMultiselect');
            //     //console.log(e.target.parentNode.closest(".tagsMultiselect"),elements)
            //     if (e.target == this.$refs.tags || e.target.parentNode == this.$refs.tags || e.target.parentNode.closest(".tagspanel") == e.target.parentNode || e.target.parentNode == e.target.parentNode.closest(".newtimetrack") || e.target.parentNode.closest(".newtimetrack") == this.$refs.tagsModals || e.target.parentNode.closest(".tagsMultiselect") == tagsMultiselect || e.target == this.$refs.tagsModals) return
            //     this.tagShow = ''
            // }
            //console.log("checkk",this.showTagsToltip)
            // if (this.showTagsToltip != '') {
            //     var tagsMultiselect = document.querySelector('.showTags');
            //     var more = document.querySelector('.linkcss')
            //     if (e.target == more || e.target.parentNode == tagsMultiselect || e.target.parentNode == e.target.parentNode.closest(".viewmoretags") || e.target.className == 'linkcss') return
            //     this.showTagsToltip = ''

            // }
            if (this.GetIndex) {
                var actionModal = document.querySelector('.actionModal');
                var openActionPopup = document.querySelector('.openActionPopup' + this.GetIndex)
                if (e.target == openActionPopup || e.target.parentNode == actionModal || e.target.parentNode == e.target.parentNode.closest(".actionModal") || e.target.className == 'fa fa-ellipsis-h action_icon openActionPopup' + this.GetIndex) return;
                this.GetIndex = ''
            }
        },
        async tracklocation(index, data) {
            this.expendCollapsedSections = [false]
            this.store.$state.expendListRow = true
            await this.store.GetTimerListingByTrackId(`?trackId=${data.trackId}&DateType=${this.store.$state.dateFilterType}`)
            this.arrayData[index] = this.getTimerListingFromTrackId ? this.getTimerListingFromTrackId[0].entryList : []
            this.listData = this.arrayData
            this.expendToggleCollapse(data.trackId)

        },
        arrayConvert(event) {
            if (event) {
                let convert = event.split(',')
                return convert
            }
            else {
                return []
            }
        },
        ToggleActionButton(index) {
            this.isActiveActionButton = !this.isActiveActionButton;
            if (index == this.GetIndex) {
                this.GetIndex = ''; // Set the index if needed
            } else {
                this.GetIndex = index
            }
        },
        expendToggleCollapse(index) {
            // Toggle the collapse state of the section at the given index
            this.$set(this.expendCollapsedSections, index, !this.expendCollapsedSections[index]);
            console.log("this.expendCollapsedSections", this.expendCollapsedSections);
        },
        isExpendCollapsed(index) {
            return this.expendCollapsedSections[index];
        },
        toggleCollapse(index) {
            // Toggle the collapse state of the section at the given index
            this.$set(this.collapsedSections, index, !this.collapsedSections[index]);
        },
        isCollapsed(index) {
            // Check if the section at the given index is collapsed
            return this.collapsedSections[index];
        },
        rowClass(rowNo) {
            let rowClass = rowNo % 2 === 0 ? "gray" : "";
            return rowClass;
        },
        tagSelectBoxOpen(id, data) {
            this.timerTags = []
            if (this.tagShow != id) {
                if (data.tagData != null) {

                    let tag = data.tagData.split(',')
                    let tagName = data.tag_name.split(',')
                    tag.map((item, index) => {
                        this.timerTags.push({
                            tagName: tagName[index],
                            tagId: Number(item)
                        })
                    })
                }
                this.tagShow = id
            } else {
                this.tagShow = ''
            }
            console.log("tagSelectBoxOpen", this.tagShow)
        },
        tagSelectBoxMobOpen(id, data) {
            this.timerTags = []
            if (this.tagMobShow != id) {
                if (data.tagData != null) {

                    let tag = data.tagData.split(',')
                    let tagName = data.tag_name.split(',')
                    tag.map((item, index) => {
                        this.timerTags.push({
                            tagName: tagName[index],
                            tagId: Number(item)
                        })
                    })
                }
                this.tagMobShow = id
            } else {
                this.tagMobShow = ''
            }
            console.log("tagSelectBoxOpen", this.tagMobShow)
        },
        async saveTags(id) {
            let tagIds = ''
            this.timerTags.map((items, index) => {
                tagIds += index != 0 ? ',' + items.tagId : items.tagId
            })
            let data = `ids=${id}&ActionType=Tag&tagIds=${tagIds}`
            await this.store.UpdateOrDeleteEntries(data)
            if (this.store.tagsSaveForListingResponse) {
                const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                this.store.getTimerListing1(url)
                const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                this.store.getTrackedtimings(url1)
                this.timerTags = []
                this.tagShow = ''
            }

        },
        pushTagsData(id, data) {
            this.showTagsToltip = id
            this.showMoreTagData = []
            let tagsArray = []
            tagsArray[id] = data.splice(2, data.length)
            this.showMoreTagData = tagsArray

        },
        pushTagsDataMob(id, data) {
            this.showTagsToltipMob = id
            this.showMoreTagData = []
            let tagsArray = []
            tagsArray[id] = data.splice(0, data.length)
            this.showMoreTagDataMob = tagsArray
            console.log("showTagsToltipMob", this.showTagsToltipMob)
        },
        entryStatusClass(entryType) {

            let entryStatusClass = "";
            switch (entryType) {
                case "TRACK_ENTRY_REJECTED":
                    entryStatusClass = "delpending";
                    manualEntry = "";
                    tip = "Rejected";
                    break;
                case "TRACK_ENTRY_UPDATE_PENDING":
                    entryStatusClass = "high-profile-bar";
                    manualEntry = "";
                    tip = "UpdateRequestPending";
                    break;
                case "TRACK_ENTRY_DELETE_PENDING":
                    entryStatusClass = "high-profile-bar";
                    manualEntry = "";
                    tip = "DeleteRequestPending";
                    break;
                case "TRACK_ENTRY_ADD_MANUAL_PENDING":
                    entryStatusClass = "high-profile-bar";
                    manualEntry = "";
                    tip = "AddManualRequestPending";
                    break;
                case "TRACK_ENTRY_APPROVED":
                    entryStatusClass = "lgtr-approved";
                    manualEntry = "";
                    tip = "Approved";
                    break;
                default:
                    entryStatusClass = "";
                    break;
            }
            return entryStatusClass
        },
        EntryTypeColor(track) {
            let trackBy = track.trackAgent
            let EntryTypeColor = "";
            switch (trackBy) {
                case "Web":
                    //  EntryTypeColor = track.pausedHours > 0 ? "gt-paused-bar" : "gt-automatic-bar";

                    EntryTypeColor = track.pausedHours > 0 && track.trackType == 'TimeTrack' && track.trackLinkedId > 0 ? "gt-orange-bar" : "";
                    break;
                case "web":
                    // EntryTypeColor = track.pausedHours > 0 ? "gt-paused-bar" : "gt-automatic-bar";
                    EntryTypeColor = track.pausedHours > 0 && track.trackType == 'TimeTrack' && track.trackLinkedId > 0 ? "gt-orange-bar" : "";
                    break;
                case "Web_Manual":
                    EntryTypeColor = "high-bar";
                    break;
                case "timesheet":
                case "timesheet_mobile":
                    EntryTypeColor = "gt-manual-bar";
                    break;
                case "AddedManualPending":
                    EntryTypeColor = "lgtr-pendingred";
                    break;
                case "Approved":
                    EntryTypeColor = "lgtr-approved";
                    break;
                case "xamarin":
                    EntryTypeColor = track.pausedHours > 0 && track.trackType == 'TimeTrack' && track.trackLinkedId > 0 ? "gt-orange-bar" : "";
                    break;
                case "Linux":
                    EntryTypeColor = track.pausedHours > 0 && track.trackType == 'TimeTrack' && track.trackLinkedId > 0 ? "gt-orange-bar" : "";
                    break;
                // case "silentapp":
                //     EntryTypeColor = track.PausedHours > 0 ? "gt-paused-bar" : "gt-automatic-bar";
                //     break;
                // case "mamarinmanual":
                //     EntryTypeColor = "gt-manual-bar";
                //     break;
                // Add other cases as needed

                default:
                    // EntryTypeColor = "gt-automatic-bar";
                    EntryTypeColor = "";
                    break;
            }
            return EntryTypeColor;
        },
        CSStrackBy(data) {

            let trackBy = "";
            switch (data.toLowerCase()) {
                case "desktop":
                case "linux":
                    trackBy = "fa fa-desktop font-23";
                    break;
                case "Web":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "web":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "web_manual":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "timesheet":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "silentapp":
                    trackBy = "fa fa-globe font-23";
                    break;
                // Add other cases as needed
                case "addedmanualpending":
                    trackBy = "fa fa-globe font-23";
                    break;
                    case "linux offline":
                    trackBy = "fa fa-desktop font-23";
                    break;
                    
                default:
                    trackBy = "fa fa-mobile font-30";
                    break;
            }
            return trackBy;
        },
        //  countinue(){
        //  this.store.submitProjectInfo(this.trackerData)
        //  }

        selectedValues(data, event) {
            if (event.target.checked) {
                console.log("this.store.$state.selectedValuesWithTypeofObject", this.store.$state.selectedValuesWithTypeofObject)
                this.store.$state.selectedValuesWithTypeofObject.push({ "trackId": data.trackId, "trackAgent": data.trackAgent })
            } else {
                this.store.$state.selectedValuesWithTypeofObject = this.store.$state.selectedValuesWithTypeofObject.filter(function (item) {
                    return item.trackId != event.target.value;
                });
                //array.find(item=>item.track)
            }
            //console.log("data",event.target.value)
            //this.store.$state.selectedValues = this.selectedCheckboxes;
        },
        // async deleteChild(id) {
        //     debugger
        //     var vm=this
        //     vm.confirmR(i18n.t("ConfirmDelete"), true, (i18n.t("Delete") + "  " + i18n.t("Entry")), function () {
        //     var params = `ids=${id}&childId=1`;
        //    debugger
        //      vm.store.deleteChildTimer(params)
        //     const url = `dateType=${vm.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
        //     vm.store.getTimerListing1(url)
        //     })
        // },
        updateIsbillable(id, ActionType) {
            var vm = this
            var params = `ids=${id}&ActionType=${ActionType}`;
            if (vm.store.getTimer.approvalStatusForWeek == 0) {
                vm.confirmR(i18n.t("ConfirmUpdate"), true, (i18n.t("Update") + "  " + i18n.t("entry")), function () {
                    DataService.setAction(params).then((response) => {
                        if (response.data == "RecordUpdatedSuccessfully") {
                            vm.ShowAlert(i18n.t('RecordUpdatedSuccessFullyBilling'), "success", true, i18n.t('Alert'));

                            const url = `dateType=${vm.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                            vm.store.getTimerListing1(url)
                            const url1 = `dateType=${vm.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                            vm.store.getTrackedtimings(url1)
                        }
                        else {
                            vm.ShowAlert(i18n.t('Failure'), "error", true, i18n.t('Alert'));
                        }
                    })
                });
            }
            else if (vm.store.getTimer.approvalStatusForWeek == 1) {
                vm.ShowAlert(this.$t("EtreesApprovedpending"), 'warning', true, this.$t('Alert'));
            }
            else {
                vm.ShowAlert(this.$t('AllowTrackingTimeOnHoliday_desc'), "warning", true, this.$t('Alert'));
            }


        },
        async duplicate(obj, index) {
            var vm = this
            console.log("duplicate", vm.store.getTimer.approvalStatusForWeek)
            if (vm.store.getTimer.approvalStatusForWeek == 0) {
                vm.confirmR(i18n.t("ConfirmDuplicateEntry"), true, (i18n.t("Delete") + "  " + i18n.t("entry")), function () {
                    vm.duplicateEntry = obj
                    vm.duplicateEntry.currentDate = dateAndTimeZone(moment(), dateTimeFromat),
                        vm.duplicateShow = vm.duplicateShow = !vm.duplicateShow
                })
            } else if (vm.store.getTimer.approvalStatusForWeek == 1) {
                vm.ShowAlert(this.$t("EtreesApprovedpending"), 'warning', true, this.$t('Alert'));
            }
            else {
                vm.ShowAlert(this.$t('AllowTrackingTimeOnHoliday_desc'), "warning", true, this.$t('Alert'));
            }
        },
        async UpdateOrDeleteEntries(id) {
            var vm = this
            await vm.store.getTimerInfo();
            var params = `ids=${id}&ActionType=Delete`;
            if (vm.store.getTimer.approvalStatusForWeek == 0) {
                vm.confirmR(i18n.t("ConfirmDelete"), true, (i18n.t("Delete") + "  " + i18n.t("entry")), function () {
                    DataService.setAction(params).then((response) => {

                        if (response.data == "DeletedSuccessfully") {
                            vm.ShowAlert(i18n.t('DeletedSuccessfully'), "success", true, i18n.t('Alert'));

                            const url = `dateType=${vm.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                            vm.store.getTimerListing1(url)
                            const url1 = `dateType=${vm.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                            vm.store.getTrackedtimings(url1)
                        }
                        else {
                            vm.ShowAlert(i18n.t('Failure'), "error", true, i18n.t('Alert'));
                        }
                        // const url = `dateType=${this.dateType}&search=&toDate=&fromDate=&newTrackId=0`
                        // this.store.getTimerListing1(url)
                    });
                });
            } else if (vm.store.getTimer.approvalStatusForWeek == 1) {
                vm.ShowAlert(this.$t("EtreesApprovedpending"), 'warning', true, this.$t('Alert'));
            }
            else {
                vm.ShowAlert(this.$t('AllowTrackingTimeOnHoliday_desc'), "warning", true, this.$t('Alert'));
            }
        },
        async editentry(item) {
            this.editTrackRecords = item
            this.editTrackRecords.PROJECT_ID = item.projectId
            this.editTrackRecords.TASK_ID = item.taskId
            this.editTrackRecords.isBillable = item.billableMark
            this.editTrackRecords.TAG_NAME = item.tag_name
            this.editTrackRecords.TAG_ID = item.tagData
            this.editTrackRecords.TRACK_DESCRIPTION = item.trackDescription
            this.editTrackRecords.TRACK_START_TIME = item.startTime
            this.editTrackRecords.TRACK_END_TIME = item.stopTime
            this.editTrackRecords.TRACK_ID = item.trackId
            this.editTrackRecords.DateSet = item.dateStart
            this.editModal = true
        }
    }
})
