<template>
    <div class="col-12 float-left mobilelistview p-0 newtimertable children" >

        <div class="activity-tab col-md-12 p-0 float-left"
            v-for="(item, index) in store.getTimerListingFromTrackId" :key="index">

            <div id="accordion" class="w-100">
                <div class="card">


                    <div class="row">
                        <div class="col-md-12">


                            <div class="col-md-12 mobilelistview_base border float-left p-0" style="background: #def1ff;"
                                v-for="(data, i) in item.entryList" :key="i">


                                <div class="col-md-12 float-left py-3" :key="i" :trackId="data.trackId">
                                    <div class="checkboxx mt-2">
                                        <div class="form-check form-check-inline" >
                                        </div>
                                    </div>
                                    <div class="contentt">
                                        <div class="d-inline-block mt-1">
                                            <a title="" value="-1" style="cursor:default;" class="text-dark">
                                                <span class="d-block font-14 font-weight-medium">{{ data.projectName
                                                    }}
                                                    :{{ data.taskName }}</span>
                                                <span class="text-dark d-block font-12">{{ data.trackDescription
                                                    }}</span>
                                            </a>
                                        </div>
                                        
                                    
                                    <div class="newtimetrack float-left w-100">


                                        <div class="dottedbod_rt w-100 row mt-2">
                                            <div class="float-left mt-1" title="" data-toggle-tooltip="tooltip"
                                            data-original-title="Click here to view track Location(s)">
                                            <em class="pl-2 pt-1 pr-3  text-secondary" :class="CSStrackBy(data.trackAgent)"
                                                style="font-size:22px;cursor:pointer;"></em>
                                        </div>
                                            <div class="float-left tagspanel">
                                                <i
                                                    :class="{ 'tt_lisitng_icons ico_listtags float-left': (data.tag_name != null), 'tt_lisitng_icons ico_listtags-o float-left': (data.tag_name == null) }"></i>
                                                <span v-for="(tags, tagIndex) of arrayConvert(data.tag_name)"
                                                    :key="tagIndex"
                                                    :class="tagIndex > 1 || showAllTags ? 'd-none' : ''">{{
                tags }}</span>

                                                <!-- Display "2 More" link only if there are more than two tags -->
                                                <a v-if="data.tag_name != null && !showAllTags"
                                                    @click="showAllTags = true" class="" href="#">2 {{ $t('More')
                                                    }}</a>

                                                <!-- Display all spans if the user has clicked the "2 More" link -->
                                                <span v-for="(tags, tagIndex) of arrayConvert(data.tag_name)"
                                                    :key="tagIndex" v-show="showAllTags">{{ tags }}</span>

                                            </div>
                                            <div class="float-left">
                                                <i
                                                    :class="{ 'tt_lisitng_icons ico_listdollar float-left': (data.billableMark == '$'), 'tt_lisitng_icons ico_listdollar-o float-left': (data.billableMark != '$') }"></i>
                                            </div>
                                            <div class="float-left" v-if='data.trackType == "Break"'>

                                                <em class="tt_lisitng_icons ico_listcup1 float-left"></em>
                                            </div>

                                        </div>

                                    </div>
</div>

                                    <div class="baselinkss">

                                        <div class="float-left">
                                            <span class="font-14 font-weight-medium d-flex pt-3">
                                                <em class="tt_lisitng_icons ico_listclock float-left mr-2"></em>
                                                {{ data.startTime }} - {{ data.stopTime }}
                                            </span>
                                        </div>
                                        <div class="timetracked float-left px-4 mt-1">
                                            <span class="mt-1">{{ data.totalHours }}</span>
                                        </div>




                                        <div :class="store.getTimer.trackAgent == 'silentapp' ? 'tt_disabled' : ''"
                                            v-if='(data.projectStatus != "INACT" || data.projectStatus != "DEL") && data.trackType == "TimeTrack" || (data.trackType == "Break" && data.takenBreak > 0 && store.getTimer.enableBreak == "yes")'>
                                            <div v-if='data.statusCode != "TRACK_ENTRY_APPROVED"'>
                                                <div class="float-left pt-3" @click="startTimer(data)"
                                                    v-if='!((data.statusCode == "TRACK_ENTRY_ADD_MANUAL_PENDING" && item.approvalTypeForTime == "Timesheet"))'>
                                                    <em class="tt_lisitng_icons ico_listplaytime float-left"
                                                        :title="$t('ContinueEntry')"
                                                        style="font-size: 22px;cursor:pointer;" :trackId=data.trackId
                                                        :trackDesc=data.trackDescription :trackBillable=data.isBillable
                                                        :projectId="data.projectId" :taskId=data.taskId></em>
                                                </div>
                                                <div v-else>

                                                    <a class="continuetimer"><em :title="$t('ContinueEntry')"
                                                            class="fa fa-clock-o text-light action-icon"
                                                            style="color: #e0e8f0 !important; font-size:22px;cursor:pointer;"></em></a>
                                                </div>
                                            </div>
                                            <div v-else>

                                                <a class="continuetimer"><em :title="$t('ContinueEntry')"
                                                        class="fa fa-clock-o text-dark action-icon"
                                                        style="font-size:22px;cursor:pointer;"></em></a>
                                            </div>
                                        </div>


                                        <div class="text-center mobile-action">
                                            <span class="actions mobileaction">
                                                <em aria-hidden="true"></em>

                                                <em ref="openActionPopup"
                                                    :class="'fa fa-ellipsis-h action_icon openActionPopup' + GetIndex"
                                                    aria-hidden="true" @click="ToggleActionButton(data.trackId)"></em>
                                                <span ref="actionModal" v-if="GetIndex === data.trackId"
                                                    class="action-list-box actionModal" style="overflow: visible;"
                                                    v-bind:style="GetIndex === data.trackId ? 'width: 393px; display: block; opacity: 1;' : 'display: none; opacity: 0;'">

                                                    <span class="list-actions p-0" id="action-list">
                                                        <a :class="store.getTimer.trackType != null ? 'tt_disabled' : ''"
                                                            class=" actions-onclick" data-toggle="modal"
                                                            @click="duplicate(data, i)" href="javascript:;"
                                                            v-if="data.trackType != 'Break'">
                                                            <!-- <em class="fa fa-copy mr-1 text-primary"></em> -->
                                                            <span class="text">{{ $t('Duplicate') }}
                                                            </span>
                                                        </a>
                                                        <a class=" actions-onclick" data-toggle="modal"
                                                            @click="UpdateOrDeleteEntries(data.trackId)"
                                                            href="javascript:;">
                                                            <!-- <em class="fa fa-trash mr-1 text-primary"></em> -->
                                                            <span class="text"> {{ $t('Delete')
                                                                }}</span>
                                                        </a>
                                                        <a v-if="data.trackType != 'Break'" class=" actions-onclick"
                                                            data-toggle="modal" @click="editentry(data)"
                                                            href="javascript:;">
                                                            <!-- <em class="fa fa-trash mr-1 text-primary"></em> -->
                                                            <span class="text"> {{ $t('Edit')
                                                                }}</span>
                                                        </a>
                                                        <!-- <em class="fa fa-times close close-action" aria-hidden="true" @click="ToggleActionButton(GetIndex)"></em> -->
                                                    </span> </span>
                                            </span>
                                        </div>
                                    </div>

                                </div>

                            </div>



                        </div>
                    </div>

                </div>
            </div>

        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import { userTimerInfo } from "../../stores/TimeTrack.js"
import DuplicateEntry from './modals/DuplicateEntry.vue';
import { storeToRefs } from 'pinia';
import i18n from '../../localization/index'
export default defineComponent({
    components: { DuplicateEntry },
    setup() {
        const store = userTimerInfo();
        const { getTimerListing, deleteChildTimer } = storeToRefs(store)
        return {
            store,
            getTimerListing,
            deleteChildTimer
        }
    },
    props: {
        list: {
            type: Array
        }
    },
    data() {
        return {
            showAllTags: false,
            selectedCheckboxes: [],
            isActiveActionButton: false,
            GetIndex: null,
            duplicateShow: false,
            duplicateEntry: ''
        }
    },
    created() {
        console.log("checkkk", this.list)
    },
    methods: {
        arrayConvert(event) {
            if (event) {
                let convert = event.split(',')
                return convert
            }
            else {
                return []
            }
        },
        ToggleActionButton(index) {
            var vm = this;
            this.isActiveActionButton = !this.isActiveActionButton;
            if (index == this.GetIndex) {
                this.GetIndex = ''; // Set the index if needed
            } else {
                this.GetIndex = index
            }
        },
        async deleteChild(id) {
            var vm = this
            vm.confirmR(i18n.t("ConfirmDelete"), true, (i18n.t("Delete") + "  " + i18n.t("Entry")), async function () {
                var params = `ids=${id}&childId=1`;
                await vm.store.deleteChildTimer(params)
                //vm.store.$state.timerListing = []
                vm.$emit('closeCollapsed')
                const url = `dateType=${vm.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                vm.store.getTimerListing1(url)
                const url1 = `dateType=${vm.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                vm.store.getTrackedtimings(url1)

            });
        },
        async duplicate(obj, index) {
            var vm = this
            vm.confirmR(i18n.t("ConfirmDuplicateEntry"), true, (i18n.t("Delete") + "  " + i18n.t("entry")), function () {
                console.log("check", obj)
                vm.$parent.duplicateEntry = obj
                //console.log("trackLinkedId",obj.trackLinkedId)
                vm.$parent.duplicateShow = true
                // vm.duplicateShow = vm.duplicateShow =!vm.duplicateShow
            })
        },
        CSStrackBy(data) {

            let trackBy = "";
            switch (data.toLowerCase()) {
                case "desktop":
                case "linux":
                    trackBy = "fa fa-desktop font-23";
                    break;
                case "Web":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "web":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "web_manual":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "timesheet":
                    trackBy = "fa fa-globe font-23";
                    break;
                // Add other cases as needed
                case "addedmanualpending":
                    trackBy = "fa fa-globe font-23";
                    break;
                default:
                    trackBy = "fa fa-mobile font-30";
                    break;
            }
            return trackBy;
        },
    },

})
</script>
<style scoped>
.action-list-box {
    opacity: 0;
    height: 0px;
    transition: opacity 1s;
}

.is-visible {
    display: inline-block;
    height: auto;
    overflow: hidden !important;
}
</style>