<template>
    <section>
        <div class="col-md-12 p-0 mt-4 float-left">
            <div class="theme-primary partition " :class="mobileView ? 'p-actions-collapse' : 'p-actions-expand'">
                <!-- <a href="javascript:;" value="list" id="pinUnpinId" class="p-action-btn text-white unpinscreen pinUnpinId" pinned="0" title="Pin" data-toggle-tooltip="tooltip" data-original-title="Pin"> <i class="fa fa-thumb-tack "></i><br></a> -->
                <span class="p-name text-white"> {{ $t(store.$state.headingInactivityTab) }}</span>
                <span class="p-actions float-right newaction_control">
                    <a ref="openFilterModal" id="aDateLable_" href="javascript:;"
