                selectedName
            }}</em></a>
                    <!-- <a @click="selectMonth" value="cal" id="ShowCalender" class="p-action-btn text-white" title="" data-toggle-tooltip="tooltip" data-original-title="Calendar View"><i class="fa fa-calendar"></i><br><span>This Month</span></a> -->
                    <a href="javascript:;" @click="graphView(true)" class="p-action-btn text-white ddlAction" id="list"><i
                            aria-hidden="true" :class="listButtonShow ? 'fa fa-list' : 'fa fa-pie-chart'"></i><br>{{
                listButtonShow ? $t('ListView') : $t('GraphView') }}</a>
                    <!-- <a href="javascript:;" v-if="!listButtonShow" @click="ShiftView(true)" class="p-action-btn text-white ddlAction"><i
                            aria-hidden="true" :class="shiftViewButtonShow?'fa fa-clock-o':'fa fa-list'"></i><br>{{
               shiftViewButtonShow? $t('ShiftView') :$t('ListView')}}</a> -->
                    <a href="javascript:;" v-if="!listButtonShow && store.getTimerListing.timeListDateGroup.length > 0"
                        @click="openmodal(true)" forvalue="Non-Billable" title=""
                        class="p-action-btn text-white ddlAction" data-toggle-tooltip="tooltip"
                        data-original-title="  Non-Billable"><i aria-hidden="true" class="fa fa-pencil"></i><br> {{
                $t('Edit') }} </a>
                    <a href="javascript:;" v-if="!listButtonShow && store.getTimerListing.timeListDateGroup.length > 0"
                        forvalue="Delete" title="" id="DeleteItem" class="p-action-btn text-white iconenable ddlAction"
                        data-toggle-tooltip="tooltip" @click="UpdateOrDeleteEntries()"><i aria-hidden="true"
                            class="fa fa-trash"></i><br>{{
                $t('DeleteEntries') }} </a>
                </span>
                <span class="collapse-head-btns">
                    <a href="javascript:;" class="collapse-btns" @click="mobileView = !mobileView;"><i
                            class="fa fa-angle-up text-white"></i></a>
                </span>
                <div></div>
            </div>
            <div ref="collabsibles" v-if="isDatePanelVisible" id="DivTimer_" data-pageaction=""
                class="DatePopUp divtimer_design p-1 R75" style="display: block;">
                <table class="table-responsive AssetDatePopUp d-block overflow-visible">
                    <tbody class="daterangebody">
                        <tr class="daterangetr">
                            <td class="sep daterangetd">
                                <ul class="date_select">
                                    <li><a @click="getListByDateType('Today', $t('Today'))" title="" id="aToday"
                                            class="Today" data-toggle-tooltip="tooltip" data-original-title="Today">{{
                $t('Today') }}</a></li>
                                    <li><a @click="getListByDateType('Yesterday', $t('Yesterday'))" title=""
                                            id="aYesterday" class="Yesterday" data-toggle-tooltip="tooltip"
                                            data-original-title="Yesterday">{{ $t('Yesterday') }}</a></li>

                                </ul>
                            </td>
                            <td valign="top" class="daterangetd">
                                <ul class="date_select">
                                    <!-- <li><a @click="getListByDateType('thisyear', $t('thisYear'))" title="" id="aAll"
                                            class="AllTime" data-toggle-tooltip="tooltip" data-original-title="All Time">{{
                                                $t('thisYear') }}</a></li>
                                    <li><a @click="getListByDateType('thismonth', $t('thisMonth'))" id="aMonth" class="Month"
                                            title="" data-toggle-tooltip="tooltip" data-original-title="This Month">{{
                                                $t('thisMonth') }}</a></li>
                                    <li><a @click="getListByDateType('LastMonth', $t('LastMonth'))" id="aLastMonth"
                                            class="LastMonth" title="" data-toggle-tooltip="tooltip"
                                            data-original-title="Last Month">{{ $t('LastMonth') }}</a></li> -->
                                    <li><a @click="getListByDateType('thisweek', $t('thisWeek'))" title="" id="aWeek"
                                            class="Week" data-toggle-tooltip="tooltip"
                                            data-original-title="This Week">{{
                $t('thisWeek') }}</a></li>
                                    <li><a @click="getListByDateType('LastWeek', $t('LastWeek'))" id="aLastWeek"
                                            class="LastWeek" title="" data-toggle-tooltip="tooltip"
                                            data-original-title="Last Week">{{ $t('LastWeek') }}</a></li>
                                </ul>
                            </td>
                        </tr>
                        <tr class="float-left p-0 m-0 mt-1 col-12 daterangetr">
                            <!-- <td class="col-8 m-1 daterangetd  float-left table_datepickerfix">
                                <label>{{ $t('From') }} ...</label><br>
                                <input id="txtFromDate_" name="startDate" type="date"
                                    class="textfield2 ie_crossicon_none FromDate form-control p-1 hasdatetimepicker notvue"
                                    autocomplete="off" data-target="#txtFromDate_" data-toggle="datetimepicker"
                                    title="Select Date" placeholder="M/D/YYYY" v-model="From">
                            </td> -->
                            <!-- <td class="col-4 m-1 daterangetd  float-left table_datepickerfix">
                                <label>...{{ $t('To') }}</label><br>
                                <input id="txtToDate_" name="endDate" type="date"
                                    class="textfield2 ie_crossicon_none ToDate form-control p-1 hasdatetimepicker notvue"
                                    autocomplete="off" data-target="#txtToDate_" data-toggle="datetimepicker"
                                    title="Select Date" placeholder="M/D/YYYY" v-model="To">
                            </td> -->

                            <td style="width:100%!important;padding:5px!important;" colspan="2"
                                class="daterangetd float-left table_datepickerfix">
                                <DatePicker class="d-flex col-8 input-group timesheetdatepicker hsstyle bg-white p-0"
                                    v-model="dateselect" :popover="false" @input="GetStartEndDates" :masks="dateMask">
                                    <template #default="{ togglePopover, inputValue, inputEvents }">
                                        <a id="aDateLable" class="pr-2 text-dark bg-white py-1 float-left">
                                            {{ DisplayWeekDate }}</a>
                                        <input id="date_range" class="form-control datepicker_HS" :value="inputValue"
                                            v-on="inputEvents" />
                                        <div class="input-group-append cursor-pointer" v-on:click="togglePopover">
                                            <span class="input-group-text">
                                                <em class="fa fa-calendar"></em>
                                            </span>
                                        </div>

                                    </template>
                                </DatePicker>
                                <input type="button" id="btnsearch_" style="margin-top:3px!important;"
                                    class="btn btn-success float-left p-1 p-1 pl-4 pr-4"
                                    :class="{ 'disabled': disabled }" data-result="btnsearch" value="Go"
                                    @click="getListByDateType('thisweek', 'This Week', 'week')">
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>

            <div class="col-md-12 px-2 py-1 float-left bg-f5f5f5" v-if="legends">
                <div class="row">
                    <!-- <div class="col-sm-12 col-md-7 col-lg-7 col-xl-7 mb-2 pt-2 responsivefix991 lft">
                        <div class="mr-2 d-inline-block">
                            <span class="legends-purple colorboxsquare"></span>{{ $t('Automatic') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="legends_low colorboxsquare"></span> {{ $t('Manual') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="legends_medium colorboxsquare"></span>{{ $t('Timesheet') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="legends_profile colorboxsquare"></span>{{ $t('Leave') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="legends_high colorboxsquare"></span> {{ $t('Holiday') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="lg-pausedresumed colorboxsquare"></span> {{ $t('PausedResumed') }}
                        </div>
                    </div>  -->
                    <div class="col-sm-12 col-md-7 col-lg-7 col-xl-7 mb-2 pt-2 responsivefix991 lft">
                        <div class="mr-2 d-inline-block">
                            <span class="legends_low colorboxsquare"></span>{{ $t('Timesheet') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="legends_high colorboxsquare"></span> {{ $t('Manual') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="lg-orange colorboxsquare"></span> {{ $t('PausedResumed') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="lg-pausedresumed colorboxsquare"></span> {{ $t('thirdparty') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="legends_profile colorboxsquare"></span>{{ $t('Pending') }}
                        </div>
                        <!-- <div class="mr-2 d-inline-block">
                        <span class="legends_medium colorboxsquare"></span>{{ $t('BulkEntry') }}
                    </div> -->
                        <div class="mr-2 d-inline-block">
                            <span class="lg-approved colorboxsquare"></span>{{ $t('ApprovedEntry') }}
                        </div>
                    </div>

                    <div class="col-sm-12 col-md-5 col-lg-5 col-xl-5 responsivefix991 rgt">
                        <div class="w-70 float-right p-0">
                            <div class="w-100 mt-0">
                                <div class="float-right p-0">
                                    <div class="background-0 text-left form-check-inline mr-0">
                                        <a id="anctimersearch" href="javascript:;" class="btn btn-success mr-1 px-4"
                                            data-original-title="Search"
                                            @click="getListByDateType(dateType, selectedName)"><i
                                                class="fa fa-search"></i></a>
                                        <a href="javascript:;" title="" id="anccleartimersearch"
                                            class="btn btn-danger texttimerrest px-4" data-toggle-tooltip="tooltip"
                                            data-original-title="Clear all "
                                            @click="clearAllFilters(dateType, selectedName)"><i
                                                class="fa fa-refresh"></i></a>
                                    </div>
                                </div>
                                <div class="col-7 float-right pl-0">
                                    <div class="form-group mb-0">
                                        <input id="texttimersearch" maxlength="50" class="form-control" type="text"
                                            placeholder="Search By Project/Task/Description" autocomplete="off"
                                            v-model="search">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

            </div>
        </div>

        <div class="col-md-12 p-0 mt-4 float-left border">
            <div class="row tt_status">

                <div class="col tt_bod-right">
                    <em class="tt_icons ico_totalhrs float-left"></em>
                    <span class="grid">
                        <span class="w-100 text-small mt-3">{{ $t('Total') }}</span>
                        <span v-html="formattedTrackedTime(store.getActivityList.totalHours)"></span>
                    </span>
                </div>

                <div class="col tt_bod-right">
                    <em class="tt_icons ico_totalpaidtime float-left"></em>
                    <span class="grid">
                        <span class="w-100 text-small mt-3">{{ $t('TotalPaidTime') }}</span>
                        <span v-html="formattedTrackedTime(store.getActivityList.paidHours)"></span>
                    </span>
                </div>

                <div class="col tt_bod-right" v-if="store.getTimer.enableBreak == 'yes'">
                    <em class="tt_icons ico_totalbreaktime float-left"></em>
                    <span class="grid">
                        <span class="w-100 text-small mt-3">{{ $t('TotalBreakTime') }}</span>
                        <span v-html="formattedTrackedTime(store.getActivityList.paidBreakHours)"></span>
                    </span>
                </div>

                <div class="col tt_bod-right" v-if="store.getTimer.enableBreak == 'yes'">
                    <em class="tt_icons ico_paidbreaktime float-left"></em>
                    <span class="grid">
                        <span class="w-100 text-small mt-3">{{ $t('PaidBreakTime') }}</span>
                        <span v-html="formattedTrackedTime(store.getActivityList.paidBreakHours)"> </span>
                    </span>
                </div>
                <div class="col tt_bod-right" v-if="store.getTimer.enableBreak != 'yes'">
                    <em class="tt_icons ico_totalpaidtime float-left"></em>
                    <span class="grid">
                        <span class="w-100 text-small mt-3">{{ $t('PausedTime') }}</span>
                        <span v-html="formattedTrackedTime( store.getActivityList.pausedHours)"></span>
                    </span>
                </div>
                <div class="col tt_bod-right" v-if="store.getTimer.enableBreak == 'yes'">
                    <em class="tt_icons ico_paidbreaktime disable float-left"></em>
                    <span class="grid">
                        <!-- <span class="w-100 text-small mt-3">{{ $t('PaidBreakTime')}}</span> -->
                        <span class="w-100 text-small mt-3">{{ $t('UnPaidBreakTime') }}</span>
                        <span  v-html="formattedTrackedTime(store.getActivityList.unPaidBreakHours)"></span>
                        <!-- <span>{{ store.getActivityList.unPaidBreakHours.split(' ')[1]}}</span> 
                        <span class="w-100 text-large"> {{ store.getActivityList.unPaidBreakHours.split(' ')[2]}} </span> 
                        <span >{{ store.getActivityList.unPaidBreakHours.split(' ')[3]}} </span> 
                        <span class="w-100 text-large">{{ store.getActivityList.unPaidBreakHours.split(' ')[4] }}</span> 
                        <span >{{ store.getActivityList.unPaidBreakHours.split(' ')[5]}}</span> -->


                    </span>
                </div>
            </div>

        </div>

        <div v-if="!shiftViewButtonShow" class="font-weight-bold mt-4 float-left ">{{ store.getActivityList.shift }}</div>

        <Alert :type="store.getAlertMessage.type" :message="store.getAlertMessage.message"
            v-bind:open="store.getAlertMessage.open" v-on:close="store.$state.getMessageAlert.open = false" />
        <!-- <div><span class="ml-3">{{ dateType }}</span></div>  -->
        <BulkEdit v-if="bulkEdit" v-on:close="openmodal(false)" />

    </section>
</template>

<script>
import {
    defineComponent,
    onMounted,
    reactive,
    ref
} from '@vue/composition-api'
import {
    userTimerInfo
} from "../../stores/TimeTrack.js"
import {
    storeToRefs
} from 'pinia';
import DataService from '../../services/DataService';
import BulkEdit from './modals/BulkEdit.vue';
import {
    dateAndTimeZone,
    dateTimeFromat,
    dateFormat,


} from "@/assets/common/vue-common.js"
import Alert from '../Common/Alert.vue';
import i18n from '../../localization/index';
import moment from 'moment'
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
export default defineComponent({
    components: {
        BulkEdit,
        Alert,
        DatePicker
    },
    props: {
        store: {
            type: Object,
            Required: true
        }
    },

    setup() {

        // onMounted(() => {
        //     if (this.listButtonShow == true) {
        //         var type = 'list'
        //     }
        //     const url = `dateType=${store.$state.dateFilterType}&type=${type}&toDate=&fromDate=`
        //     this.getTrackedtimings(url)

        // })
        return {

        }
    },
    created: function () {
        var type = ''
        if (this.listButtonShow == true) {
            type = 'graph'
        } else {
            type = 'list'
        }
        this.FetchData(type)

    },
    mounted() {
        document.addEventListener("click", this.onClickOutside);
    },
    beforeDestroy() {
        document.removeEventListener("click", this.onClickOutside);
    },

    data() {
        return {
            dateType: this.store.$state.dateFilterType,
            isDatePanelVisible: false,
            From: '',
            To: '',
            search: '',
            bulkEdit: false,
            selectedName: i18n.t('Today'),
            listButtonShow: false,
            legends: true,
            DisplayWeekDate: i18n.t('SelectWeek'),
            dateselect: dateAndTimeZone(moment(), dateTimeFromat),
            dateMask: {
                input: dateFormat,
            },
            selectDays: false,
            disabled: true,
            mobileView: false,
            shiftViewButtonShow: true,

        }
    },

    methods: {
        //if click out side scope then auto close popup
        onClickOutside(event) {
            if (this.isDatePanelVisible == true) {
                const {
                    collabsibles,
                    openFilterModal
                } = this.$refs;
                if (!collabsibles || collabsibles.contains(event.target) || !openFilterModal || openFilterModal.contains(event.target)) return;
                this.isDatePanelVisible = false
            }
        },
        async FetchData(event) {
            const url = `dateType=${this.store.$state.dateFilterType}&type=${event}&toDate=${this.To}&fromDate=${this.From}&graphFor=${'mystatus'}`
            await this.store.getTrackedtimings(url)
        },
        async graphView() {
            if (this.listButtonShow == true) {
                this.legends = true
                this.shiftViewButtonShow=true
                this.FetchData('graph')
            } else {
                this.legends = false
                this.shiftViewButtonShow=true
            }

            this.listButtonShow = this.listButtonShow = !this.listButtonShow
            this.store.viewType=false
            //console.log("graph", this.listButtonShow)
            //this.FetchData('graph')
            this.$emit("graphView", this.listButtonShow)

        },
        async ShiftView() {
            var vm = this;
            vm.shiftViewButtonShow=vm.shiftViewButtonShow = !vm.shiftViewButtonShow
            vm.store.viewType=true
            vm.$emit("ShiftView",  vm.shiftViewButtonShow)
            if(vm.shiftViewButtonShow){
            const url = `dateType=${vm.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0&viewType=ShiftView`
            await vm.store.getTimerListing1(url)
            const url1 = `dateType=${vm.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=&viewType=ShiftView`
            vm.store.getTrackedtimings(url1)
        }

        },
        selectMonth() {
            this.$emit("selectMonth", this.dateType)
        },
        UpdateOrDeleteEntries() {
            var vm = this;
            if (vm.store.getTimer.approvalStatusForWeek == 0) {
                if (this.store.$state.selectedValues.length > 0) {
                    var id = this.store.$state.selectedValues
                    var params = `ids=${id}&ActionType=Delete`;
                    vm.confirmR(i18n.t("ConfirmDelete"), true, (i18n.t("Delete") + "  " + i18n.t("entry")), function () {
                        DataService.setAction(params).then((response) => {
                            //var data=response
                            if (response.data == "DeletedSuccessfully") {
                                vm.ShowAlert(i18n.t('DeletedSuccessfully'), "success", true, i18n.t('Alert'));
                                vm.store.$state.selectedValues = []
                                const url = `dateType=${vm.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                                vm.store.getTimerListing1(url)
                                vm.FetchData('Graph')
                            } else {
                                vm.ShowAlert(i18n.t('Failure'), "error", true, i18n.t('Alert'));
                            }

                        });
                    });
                } else {

                    this.store.alertBOxForMessage({
                        "type": 'warning',
                        "open": true,
                        "message": i18n.t('PleaseSelectCheckBox')
                    })

                }
            } else if (vm.store.getTimer.approvalStatusForWeek == 1) {
                vm.ShowAlert(this.$t("EtreesApprovedpending"), 'warning', true, this.$t('Alert'));
            }
            else {
                vm.ShowAlert(this.$t('AllowTrackingTimeOnHoliday_desc'), "warning", true, this.$t('Alert'));
            }
        },
        toggleDatePanel() {
            this.isDatePanelVisible = !this.isDatePanelVisible;
        },
        async getListByDateType(type, name, go = '') {

            this.selectDays = true
            if (type == 'Today' || type == 'thisweek') {
                this.dateselect = new Date()
            } else if (type == 'Yesterday') {
                this.dateselect = new Date()

                this.dateselect = this.dateselect.setDate(this.dateselect.getDate() - 1);

            }
            this.store.loaderStatus(true)
            if (go != 'week') {
                this.DisplayWeekDate = i18n.t('SelectWeek')
            }


            this.dateType = type
            this.store.$state.dateFilterType = type
            this.store.$state.From = this.From
            this.store.$state.To = this.To
            console.log("this.listButtonShow",this.listButtonShow)

            if(this.shiftViewButtonShow){
            if (!this.listButtonShow) {
                const url = `dateType=${this.dateType}&search=${this.search}&toDate=${this.To}&fromDate=${this.From}&newTrackId=0`
                await this.store.getTimerListing1(url)
                
                this.store.$state.timerlistingByTrackId=[]
                
                // const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                // this.store.getTrackedtimings(url1)
                this.FetchData('list')
                this.isDatePanelVisible = false
            } else {
                this.store.$state.dateFilterType = ''
                //var params = `dateType=${this.store.$state.dateFilterType}&toDate=${this.store.$state.To}&fromDate=${this.store.$state.From}`
                // this.store.TimerTeamStatus(params)
                this.isDatePanelVisible = false;
                setTimeout(() => {
                    this.store.$state.dateFilterType = type
                }, 100)

            }
        }
        else{
            //-----------------shift and list view according to filters------------------------

            console.log("this.shiftViewButtonShow",this.shiftViewButtonShow)
            if(!this.shiftViewButtonShow){
                const url = `dateType=${this.dateType}&search=${this.search}&toDate=${this.To}&fromDate=${this.From}&newTrackId=0&viewType=ShiftView`
                await this.store.getTimerListing1(url)
                const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=&viewType=ShiftView`
                this.store.getTrackedtimings(url1)
                this.isDatePanelVisible = false
            }
            else{
                const url = `dateType=${this.dateType}&search=${this.search}&toDate=${this.To}&fromDate=${this.From}&newTrackId=0`
                await this.store.getTimerListing1(url)
                this.store.$state.timerlistingByTrackId=[]
                this.FetchData('list')
                this.isDatePanelVisible = false
            }


        }

           // ----------------------------------------------------------------------------------
            this.selectedName = name
            this.To = ''
            this.From = ''
            //this.dateType=''
            //setTimeout(()=>{
            this.store.loaderStatus(false)
            //},10)
            setTimeout(() => {
                this.selectDays = false
            }, 1000)

        },
        openmodal(event) {
            var vm = this;
            if (vm.store.getTimer.approvalStatusForWeek == 0) {
                if (event) {
                    if (vm.store.$state.selectedValues.length > 0) {
                        if (vm.store.getTimer.isEdit == "yes") {
                            vm.bulkEdit = event
                        } else {
                            console.log("vm.store.$state.selectedValuesWithTypeofObject", vm.store.$state.selectedValuesWithTypeofObject)
                            let obj = vm.store.$state.selectedValuesWithTypeofObject.find(item => item.trackAgent != "Web_Manual")
                            console.log("obj",vm.store.$state.selectedValues)
                            if (obj) {
                                vm.ShowAlert(i18n.t('AutomaticEntryNotAllow'), "warning", true, i18n.t('Alert'));

                            } else {
                                vm.bulkEdit = event
                            }
                        }

                    } else {
                        vm.ShowAlert(i18n.t('PleaseSelectCheckBox'), "warning", true, i18n.t('Alert'));
                        // vm.store.alertBOxForMessage({
                        //     "type": 'warning',
                        //     "open": true,
                        //     "message": i18n.t('PleaseSelectCheckBox')
                        // })
                    }
                } else {
                    vm.bulkEdit = event
                }
                // bulkEdit=!bulkEdit
            } else if (vm.store.getTimer.approvalStatusForWeek == 1) {
                vm.ShowAlert(this.$t("EtreesApprovedpending"), 'warning', true, this.$t('Alert'));
            }
            else {
                vm.ShowAlert(this.$t('AllowTrackingTimeOnHoliday_desc'), "warning", true, this.$t('Alert'));
            }
        },
        clearAllFilters(type, name) {
            this.search = ''
            this.dateType = type
            this.store.$state.dateFilterType = type
            const url = `dateType=${this.dateType}&search=${this.search}&toDate=${this.To}&fromDate=${this.From}&newTrackId=0`
            this.store.getTimerListing1(url)
            const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
            this.store.getTrackedtimings(url1)
            this.selectedName = name
        },
        GetStartEndDates: async function (event, id, a) {
            var VueObj = this;

            if (VueObj.selectDays == false) {

                VueObj.DisplayWeekDate = "";
                let url = `DateSpan=thisweek&report=`;
                await DataService.GetStartEndDate(url).then(response => {
                    VueObj.From = response.data.dateStart;
                    VueObj.To = response.data.dateEnd;

                    VueObj.DisplayWeekDate = response.data.displayText;
                    VueObj.StartWeekDay = response.data.startWeekDay;
                    //VueObj.StartWeekDay ="wednesday";
                });
                //VueObj.FormSchema[0].Data[0].placeholder = "Date Range: " + VueObj.DisplayWeekDate;
                var offset = 1;
                switch (VueObj.StartWeekDay.toLowerCase()) {
                    case 'tuesday':
                        offset = 2;
                        break;
                    case 'wednesday':
                        offset = 3;
                        break;
                    case 'thursday':
                        offset = 4;
                        break;
                    case 'friday':
                        offset = 5;
                        break;
                    case 'saturday':
                        offset = 6;
                        break;
                    case 'sunday':
                        offset = 0;
                        break;
                    default:
                        offset = 1;
                        break;
                }

                var today = new Date(this.dateselect);
                if (today == '') {
                    today = new Date(dateAndTimeZone(moment(), dateTimeFromat));
                }
                if (this.dateselect) {
                    //var currentDay = today.getDay() - offset;
                    var currentDay = today.getDay() == 0 ? 6 - offset : today.getDay() - offset
                    var startdate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay);
                    var enddate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay + 6);
                    var dateTime = moment(new Date(VueObj.$options.filters.formatDate(startdate)), dateTimeFromat).format(dateFormat) + ' to ' + moment(new Date(VueObj.$options.filters.formatDate(enddate)), dateTimeFromat).format(dateFormat);
                    VueObj.From = VueObj.$options.filters.formatDate(startdate);
                    VueObj.To = VueObj.$options.filters.formatDate(enddate);
                    VueObj.DisplayWeekDate = "";
                    VueObj.DisplayWeekDate = dateTime;

                    //for enable go button when we select date.
                    if (VueObj.DisplayWeekDate != i18n.t('SelectWeek')) {
                        VueObj.disabled = false;
                    }
                }
                VueObj.selectDays = false
            }

        },
        formattedTrackedTime(val) {
            val= !val?'00 h 00 mins 00 secs':val
            const parts = val.split(' ')
            return parts.map((part, index) => {
                if (index % 2 === 0) {
                    return `<span class="text-large">${part}</span>`;
                } else {
                    return `<span class="font-weight-medium font-15">${part}</span>`;
                }
            }).join(' ');
        }

    }
})
</script>
