<template>
    <div>
        <div class="my-popups">
            <div class="modal d-block ">

                <div class="modal-dialog modal-dialog-centered ui-draggable" style="display: inline-block; max-width: 500px;">
            <div class="modal-content">
                        <div class="modal-header ui-draggable-handle">
                            <h5 id="ui-id-26" class="modal-title">{{ $t('UpdateTimer') }} </h5><button type="button"
                                title="" @click="$emit('close')" class="close" data-original-title="Close"><i
                                    class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div id="backgroundPopup" class="modal-body"
                            style="height: auto; min-height: 0px; max-height: none;">
                            <ValidationObserver ref="form" v-slot="{ handleSubmit }" class="listing py-3 px-3">
                                <form v-on:submit.prevent="handleSubmit(submitForm)" ref="form">

                                    <div class="row">
                                        <div class="col-md-2 p-0"><label class="">{{ $t('ProjectTitle') }}:</label>
                                        </div>
                                        <div class="col-md-9">
                                            <ValidationProvider v-slot="{ errors }">
                                                <multiselect :placeholder="$t('searchProject')" @input="taskByProject"
                                                    v-model="trackerData.projectId" :options="store.getProjectsByUser"
                                                    label="projectName" track-by="projectId" :show-labels="false">
                                                    <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                                </multiselect>
                                                <span>{{ errors[0] ? $t('DescRequired') : '' }}</span>
                                            </ValidationProvider>
                                        </div>

                                    </div>

                                    <div class="row">
                                        <div class="col-md-2 p-0"><label class="">{{ $t('Tasks') }}:</label></div>
                                        <div class="col-md-9">
                                            <multiselect @input="isBillableSatus" :placeholder="$t('searchTask')"  v-model="trackerData.taskId" :options="store.getTasksFromProject" label="firstAttribute" track-by="value" :show-labels="false">
                                                                        <template slot="option" slot-scope="props">
                                                                            <div class="option__desc"><span class="option__title">{{
                                                                                props.option.firstAttribute }}</span>
                                                                                <label class="float-right text-light px-2 ml-1 taskcount">{{ props.option.subTasksCount }}</label>
                                                                                <label class="float-right"> {{ props.option.thirdAttribute }}% </label>
                                                                            </div>
                                                                        </template>  </multiselect>
                                        </div>

                                    </div>

                                    <div class="row">
                                        <div class="col-md-2 p-0"><label class="">{{ $t('Tags') }}:</label></div>
                                        <div class="col-md-9">
                                            <multiselect :placeholder="$t('searchTags')" :multiple="true"
                                                v-model="tagSelect" :options="store.getTags" label="tagName"
                                                track-by="tagId" :show-labels="false">
                                                <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                            </multiselect>
                                        </div>
                                    </div>

                                    <div class="row">

                                        <div class="col-md-2 p-0"><label>{{ $t('Billable') }}:</label></div>
                                        <div class="col-md-9"><label class="switch">
                                                    <input type="checkbox" id="IsbillableN"
                                                        v-model="trackerData.isBillable" name="Isbillable"
                                                        class="dynamic custom-switch"><span class="slider round">
                                                        <span class="slider-yes">Yes</span><span
                                                            class="slider-no">No</span></span></label>
                                        </div>
                                    </div>

                                    <div class="row mt-2">
                                        <div class="col-md-2 p-0"><label class="">{{ $t('Description') }}:</label></div>
                                        <div class="col-md-9">
                                            <ValidationProvider v-slot="{ errors }">
                                                <textarea id="Taskdescription" name="Description"
                                                    v-model="trackerData.trackDescription"
                                                    style="border-color: #BDBCBD; resize:none" rows="10" cols="2000"
                                                    class="timertxtarea form-control"
                                                    :class="errors[0] ? 'is-invalid' : ''"
                                                    :placeholder="$t('PleaseEnterDescription')"
                                                    maxlength="2000"></textarea>
                                                <span class="text-danger font-weight-bold">{{ errors[0] }}</span>
                                            </ValidationProvider>
                                        </div>
                                    </div>
                                    <div class="row mt-2">
                                        <div class="col-md-2 p-0"><label class="">{{ $t('StartTime') }}:</label></div>
                                        <div class="col-md-9">
                                            <ValidationProvider v-slot="{ errors }">
                                                <input type="time" v-model="startTime" class="w-100">
                                                <span class="text-danger font-weight-bold">{{ errors[0] }}</span>
                                            </ValidationProvider>
                                        </div>
                                    </div>
                                    <div class="row mt-2">
                                        <div class="col-md-2 p-0"><label class="">{{ $t('StopTime') }}:</label></div>
                                        <div class="col-md-9">
                                            <ValidationProvider v-slot="{ errors }">
                                                <input type="time" v-model="endTime" class="w-100">
                                                <span class="text-danger font-weight-bold">{{ errors[0] }}</span>
                                            </ValidationProvider>
                                        </div>
                                    </div>

                                    <div class="row flex-row-reverse mt-2">
                                        <div class="text-end text-right col-12">
                                            <button type="submit" class="btn btn-success"><i
                                                    class="fa fa-save pr-2"></i>Save</button>
                                            <button @click="$emit('close')" type="button" class="btn btn-danger"
                                                style="margin-left: 5px;"><i
                                                    class="fa fa-close pr-2"></i>Cancel</button>
                                        </div>

                                    </div>

                                </form>
                            </ValidationObserver>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {
    defineComponent,
    reactive,
    ref,
    toRefs
} from '@vue/composition-api'
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
import moment from "moment"
import VueTimepicker from 'vue2-timepicker'
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
import Alert from '../../Common/Alert.vue';
import DataService from '@/services/DataService.js'
import i18n from '@/localization/index'
import {
    getSecondsFromDate,
    formatter,
    dateAndTimeZone,
    dateTimeFromat,
    dateFormat,
} from "@/assets/common/vue-common.js"
extend('required', {
    validate(value) {
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };
    },
    message: '{_field_} is required',
    // This rule reports the `required` state of the field.
    computesRequired: true
});
export default defineComponent({
    name: "Update Timer",
    components: {
        ValidationProvider,
        ValidationObserver,
        VueTimepicker,
        DatePicker,
        Alert
    },
    props: {
        store: {
            type: Object
        },
        editTrackRecords: {
            type: Object
        }
    },
    created() {
        this.fetchData();
        this.store.$state.tasks = []
    },
    mounted() { },
    methods: {
        async fetchData() {

            this.store.tagGetForTimer();
            Object.assign(this.formData, this.trackerData);
            await this.store.getProjects()
            await this.store.getTimerInfo()
            this.editTrackRecords.PROJECT_ID != 0 ? this.projectId() : this.trackerData.projectId = null
            if (this.editTrackRecords.TASK_ID != 0) {
                await this.store.loadTasks(this.editTrackRecords.PROJECT_ID);
                this.taskId()
            } else {
                this.trackerData.taskId = null
            }

            if (this.editTrackRecords.TAG_ID) {
                let tagId = this.editTrackRecords.TAG_ID.split(',')
                let tagName =this.editTrackRecords.TAG_NAME.split(',')

                tagId.map((item,index) => {
                    this.tagSelect.push({
                        tagName:tagName[index],
                        tagId:Number(item)
                })
                })
            }
            
            this.trackerData.trackDescription = this.editTrackRecords.TRACK_DESCRIPTION
            let dateStart = moment(new Date(this.trackerData.DateSet), "YYYY-MM-DD").format("YYYY-MM-DD")
            this.startTime = moment(dateStart +' '+ this.editTrackRecords.TRACK_START_TIME, "YYYY-MM-DD hh:mm:ss A").format("HH:mm")
            this.endTime = moment(dateStart +' '+ this.editTrackRecords.TRACK_END_TIME, "YYYY-MM-DD hh:mm:ss A").format("HH:mm")
            this.trackerData.DateSet = this.editTrackRecords.DateSet
            this.trackerData.isBillable=this.editTrackRecords.isBillable=="$"?true:false
        },
        async submitForm() {
            if (this.validHours()) {
                this.tagSelect.map((items, index) => {
                    this.trackerData.TrackTagIds += index != 0 ? ',' + items.tagId : items.tagId
                })
                console.log("tags",this.tagSelect)
                this.trackerData.trackId = this.editTrackRecords.TRACK_ID
                this.trackerData.projectId = this.trackerData.projectId ? this.trackerData.projectId.projectId : 0
                this.trackerData.taskId = this.trackerData.taskId ? this.trackerData.taskId.value : 0
                let dateStart = moment(new Date(this.trackerData.DateSet), "YYYY-MM-DD").format("YYYY-MM-DD")
                let startDateTime = moment(dateStart + ' ' + this.startTime, "YYYY-MM-DD HH:mm")
                let endDateTime = moment(dateStart + ' ' + this.endTime, "YYYY-MM-DD HH:mm")
                this.trackerData.startTime=startDateTime.format("YYYY-MM-DD HH:mm:ss")
                this.trackerData.endTime=endDateTime.format("YYYY-MM-DD HH:mm:ss")
                console.log("trackerData",this.trackerData)
                  await this.store.updateTrackEntry(this.trackerData)
                  if (this.store.getUpdateTrackEntryRes.status == 'TimerEntryUpdated') {
                      const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                      this.store.getTimerListing1(url)
                      this.$emit('close')

                  }
            }

        },
        async deleteEntry() {
            var vm = this;
            var params = `ids=${this.editTrackRecords.TRACK_ID}&ActionType=Delete`;
            DataService.setAction(params).then((response) => {
                //var data=response
                if (response.data == "DeletedSuccessfully") {
                    vm.ShowAlert(i18n.t('DeletedSuccessfully'), "success", true, i18n.t('Alert'));
                    this.$emit('close')
                } else {
                    vm.ShowAlert(i18n.t('Failure'), "error", true, i18n.t('Alert'));
                }

            });
        },
        taskByProject() {
            this.store.$state.tasks = []
            this.trackerData.taskId = ''
            if (this.trackerData.projectId) {
                this.store.loadTasks(this.trackerData.projectId.projectId)
            } else {
                //getTasksFromProject = []
            }

        },
        isBillableSatus() {
            this.trackerData.isBillable = this.trackerData.taskId.isBillable
             //console.log("checkk",this.trackerData.taskId.isBillable)
        },
        // Description text length counter
        textCounter() {
            this.textCountertlength = 2000 - this.trackerData.trackDescription.length
        },
        //If project id get number from backend then set custom object for multydropdown
        projectId() {
            this.trackerData.projectId = this.editTrackRecords.PROJECT_ID ? this.store.getProjectsByUser.find((data) => data.projectId == this.editTrackRecords.PROJECT_ID) : 0
        },
        //If task id get number from backend then set custom object for multydropdown
        taskId() {
            this.trackerData.taskId = this.editTrackRecords.TASK_ID ? this.store.getTasksFromProject.find((data) => data.value == this.editTrackRecords.TASK_ID) : 0
        },
        validHours() {
            let status = false
            if (!this.startTime) {
                this.status = false
                this.store.alertBOxForMessage({
                    "type": 'warning',
                    "open": true,
                    "message": "Please Enter start Date"
                })
            } else if (!this.endTime) {
                this.status = false
                this.store.alertBOxForMessage({
                    "type": 'warning',
                    "open": true,
                    "message": "Please Enter End Date"
                })
            }
            //console.log("end",moment(moment(this.endTime.HH+':'+this.endTime.mm, 'hh:mm')))
            else if (this.startTime && this.endTime) {

                let dateStart = moment(new Date(this.trackerData.DateSet), "YYYY-MM-DD").format("YYYY-MM-DD")
                let startDateTime = moment(dateStart + ' ' + this.startTime, "YYYY-MM-DD HH:mm")
                let endDateTime = moment(dateStart + ' ' + this.endTime, "YYYY-MM-DD HH:mm")
                let today = moment(dateAndTimeZone(moment(), dateFormat))
                    status = true
                    let sec = getSecondsFromDate({
                        startDate: moment(this.startTime, 'hh:mm'),
                        endDate: moment(this.endTime, 'hh:mm')
                    }, 'seconds')

                    this.trackerData.startDate = startDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.trackerData.DateSet = startDateTime.format("YYYY-MM-DD HH:mm:ss")
                    //this.manualData.SelectedDate = startDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.trackerData.startTime = startDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.trackerData.stopTime = endDateTime.format("YYYY-MM-DD HH:mm:ss")
                    // this.manualData.endDate = endDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.totalHours = formatter(sec)
                    this.trackerData.totalHours = sec
                
            }
            return status
        },
        //Get time time diffrece between start date and end date
        getTimeDiff() {
            if (this.startTime && this.endTime) {
                let dateStart = moment(new Date(this.trackerData.DateSet), "YYYY-MM-DD").format("YYYY-MM-DD")
                let startDateTime = moment(dateStart + ' ' + this.startTime, "YYYY-MM-DD HH:mm")
                let endDateTime = moment(dateStart + ' ' + this.endTime, "YYYY-MM-DD HH:mm")
                let today = moment(dateAndTimeZone(moment(), dateFormat))
                if (startDateTime.valueOf() > endDateTime.valueOf() || endDateTime.valueOf() > today.valueOf()) {
                    this.totalHours = "00:00:00"
                } else {
                    let sec = getSecondsFromDate({
                        startDate: moment(this.startTime, 'hh:mm'),
                        endDate: moment(this.endTime, 'hh:mm')
                    }, 'seconds')
                    this.totalHours = formatter(sec)
                }
            }
        }

    },
    data() {
        return {
            dateMask: {
                input: dateFormat,
            },
            tagSelect: [],
            formData: {},
            textCountertlength: 2000,
            startTime: '00:00',
            endTime: '00:00',
            date: dateAndTimeZone(moment(), dateFormat),
            totalHours: "00:00:00",
            trackerData: {
                projectId: 0,
                trackDescription: "",
                taskId: 0,
                isBillable: false,
                format: false,
                trackId: 0,
                startTime: dateAndTimeZone(moment(), dateTimeFromat),
                DateStart: dateAndTimeZone(moment(), dateTimeFromat),
                DateSet: dateAndTimeZone(moment(), dateTimeFromat),
                stopTime: dateAndTimeZone(moment(), dateTimeFromat),
                currentDate: dateAndTimeZone(moment(), dateTimeFromat),
                createdBy: this.store.getUserInformation.id,
                totalHours: 0,
                timeformat: "12",
                TagIds: null,
                type: "TimeTrack",

            }
        }
    }
})
</script>