<template>
    <section :style="'--accent-time:' + breakTimerSecond">


        <div class="container-fluid p-0">
            <div class="col-md-12 p-0 mt-2">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white">{{ $t('trackTime') }}</span>
                    <span class="p-actions float-right">
                        <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info"
                            :class="{ 'tt_disabled': store.getTimer.statusCode }"
                            @click="$emit('addManualTime', true)"><i class="fa fa-clock-o mx-2"></i><span>{{
        $t('AddManually') }}</span></a>
                    </span>
                </div>
            </div>
            <ValidationObserver ref="form" v-slot="{ handleSubmit }">

                <form v-on:submit.prevent="handleSubmit(submit)" ref="form"
                    :class="store.getTimer.trackAgent == 'silentapp' ? 'tt_disabled' : ''">
                    <div class="border bg-light p-3 newtimetrack mt-2 float-left w-100">
                        <div class="basebod bg-white float-left w-100">
                            <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left leftcont_divs">
                                <div class="row">
                                    <multiselect :placeholder="$t('searchProject')" id="Project"
                                        class="form-group tt_ddr_1 col border-0 tt_bod-right Project" @input="taskByProject"
                                        v-model="trackerData.projectId" :options="getProjectsByUser" label="projectName"
                                        track-by="projectId" :show-labels="false" 
                                        :class="store.getTimer.trackType == 'Break' ? 'tt_disabled' : ''">
                                        <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                    </multiselect>
                                    <!-- <multiselect :placeholder="$t('searchTask')"
                                        class="form-control tt_ddr col border-0 tt_bod-right" id="exampleFormControlSelect2"
                                        v-model="trackerData.taskId" :options="getTasksFromProject" label="firstAttribute"
                                        track-by="value" :show-labels="false" @input="isBillableSatus"
                                        :class="store.getTimer.trackType == 'Break' ? 'tt_disabled' : ''">
                                        
                                        <template slot="option" slot-scope="props" :disabled="true">
                                            <div class="option__desc">
                                                <span class="option__title" :disabled="true" :class="props.option.parentValue > 0 ? 'child' : ''">{{
                                                props.option.firstAttribute }}</span>
                                                <label class="float-right text-light px-2 ml-1 taskcount" >{{
                                                    props.option.subTasksCount }}</label>
                                                <label class="float-right"> {{ props.option.thirdAttribute }}% </label>
                                            </div>
                                        </template>
<span slot="noResult">{{ $t('No_Record_Found') }}</span>
</multiselect> -->
                                    <Treeselect v-model="trackerData.taskId"
                                        class="form-control tt_ddr tt_ddr_new col border-0 tt_bod-right multiselect"
                                        :multiple="false" :options="getTasksFromProject" placeholder="Unassigned Task"
                                        @input="isBillableSatus" id="Task"
                                        :class="store.getTimer.trackType == 'Break' ? 'tt_disabled' : ''">
                                        <label slot="option-label" slot-scope="{ node }" :class="labelClassName"
                                            class="d-block my-1">
                                            <div class="option__desc">
                                                <span class="option__title"
                                                    :class="node.raw.parentValue > 0 ? 'child' : ''">{{
        node.raw.firstAttribute }}</span>
                                                <label class="float-right text-light px-2 ml-1 taskcount">{{
        node.raw.subTasksCount }}</label>
                                                <label class="float-right"> {{ node.raw.thirdAttribute }}% </label>
                                            </div>
                                        </label>
                                    </Treeselect>
                                    <div class="p-0 position-relative">

                                        <a class="tt_p-action-btn tt_bod-right" id="Tags"
                                            :class="store.getTimer.trackType == 'Break' ? 'tt_disabled' : ''"
                                            @click="tagShow = tagShow = !tagShow; tagOpenModal(); breakModalPopup = false"
                                            ref="tags">
                                            <i class="tt_icon ico_tag"></i><span class="text-uppercase"> {{
                                             tagSelect.length
                                                 > 0 ? tagSelect.length + ' ' + $t('selected') : $t('tags') }}</span>
                                        </a>
                                        <a class="tt_p-action-btn tt_bod-right " id="isBillable"
                                            :class="store.getTimer.trackType == 'Break' ? 'tt_disabled' : ''"
                                            @click="trackerData.isBillable = !trackerData.isBillable">
                                            <i
                                                :class="trackerData.isBillable ? 'tt_icon ico_dollar' : 'tt_icon ico_dollar-o'"></i><span
                                                class="text-uppercase">{{ trackerData.isBillable ? 'Paid' : 'Unpaid'
                                                }}</span>
                                        </a>
                                        <a ref="break" class="tt_p-action-btn tt_bod-right" id="Break"
                                            @click="breakModalOpen({ breakTimer: false, modalClose: true }); tagShow = false"
                                            :class="store.getTimer.enableBreak == 'yes' ? store.getTimer.trackType == 'Break' ? 'tt_disabled' : '' : 'd-none'">
                                            <i class="tt_icon ico_cutlery"></i><span class="text-uppercase">{{
        $t('Break')
    }}</span>
                                        </a>

                                        <div class="hs_tag tagsModals" v-if="tagShow" ref="tagsModals">
                                            <multiselect ref="tagsMultiselect" :close-on-select="false"
                                                :placeholder="$t('searchTags')" :multiple="true"
                                                class="form-group  border-0 tt_bod-right" v-model="tagSelect"
                                                :options="store.getTags" label="tagName" track-by="tagId"
                                                :show-labels="false" id="exampleFormControlSelect1"
                                                :class="store.getTags.length > 0 ? 'tt_ddr_1 col' : ''">
                                                <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                            </multiselect>
                                            <div data-v-0f960004="" class="ml-2 squarebtn">
                                                <a data-v-0f960004="" class="round-icon-small btn-info border-radius-0" id="AddTags"
                                                    @click="openTagsAdd(true)"><i data-v-0f960004="" alt="+"
                                                        class="fa fa-plus text-white pt-7 font-18"></i></a>
                                                <a data-v-0f960004=""
                                                    class="round-icon-small btn-danger border-radius-0"
                                                    @click="clearTags"><i data-v-0f960004="" alt="+"
                                                        class="fa fa-refresh text-white pt-7 font-18"></i></a>
                                            </div>
                                            <div data-v-0f960004="" class="ml-2 squarebtn"></div>

                                        </div>

                                        <div class="hs2 breakModal" v-if="breakModalPopup" ref="breakModal">
                                            <BreakModal ref="breakModal" v-if="breakModalPopup"
                                                v-on:close="breakModalPopup = false" v-on:startBreak="startbreak"
                                                v-bind:userId="store.getUserInformation.id" v-bind:timerModule="true"
                                                v-bind:trackDataPouse="trackerData" v-bind:automaticBreak="true"
                                                v-bind:tagSelect="tagSelect"
                                                @GetPaidOrUnpaidBreak="GetPaidOrUnpaidBreak">
                                            </BreakModal>
                                        </div>
                                    </div>

                                </div>

                            </div>

                            <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left rightcont_divs">
                                <div class="row">
                                    <ValidationProvider :rules="trackerData.projectId ? '' : 'required'"
                                        v-slot="{ errors }" class="tt_textarea_span position-relative  "
                                        :class="timerOrBreak && store.getTimer.enableBreak != 'yes' ? 'tt_textarea_stop' : timerOrBreak ? '' : ''">
                                        <textarea v-if="store.getTimer.trackType != 'Break'" id="Taskdescription"
                                            name="Description" v-model="trackerData.trackDescription"
                                            class="form-control tt_textarea"
                                            :class="errors[0] ? 'tt_textarea is-invalid' : timerOrBreak ? 'tt_textarea-stop' : 'tt_textarea'"
                                            style="resize:none" v-on:keydown="textCounter" v-on:keyup="textCounter"
                                            v-on:mouseout="descriptionChange(); textCounter()"
                                            :placeholder="$t('PleaseEnterDescription')" maxlength="2000"
                                            data-vv-delay="100"></textarea>
                                        <textarea v-else id="Taskdescription" name="Description"
                                            v-model="store.getTimer.breakName"
                                            class="form-control tt_textarea tt_disabled"
                                            :class="errors[0] ? 'tt_textarea is-invalid ' : timerOrBreak ? 'tt_textarea-stop' : 'tt_textarea'"
                                            style="resize:none" v-on:keydown="textCounter" v-on:keyup="textCounter"
                                            v-on:mouseout="textCounter" :placeholder="$t('PleaseEnterDescription')"
                                            maxlength="2000"></textarea>
                                        <span class="val_msg">{{ errors[0] ? $t('DescRequired') : '' }}</span>
                                    </ValidationProvider>
                                    <div class="tt_play_btnbase"  id="Start" v-if="store.getTimer.trackType == null">
                                        <button class="tt_play_btn tt_bod-right" id="Start" type="submit">
                                            <i class="fa fa-play text-white"></i>
                                        </button>
                                    </div>
                                    <div class="tt_play_btnbase" v-else>
                                        <a v-if="store.getTimer.statusCode == 'ACT' && store.getTimer.enableBreak != 'yes'"
                                            class="tt_play_btn tt_bod-right mr-1 bg-info" @click="pouseTimer()">
                                            <i class="fa fa-pause text-white"></i>
                                        </a>
                                        <a v-if="store.getTimer.statusCode == 'PAUS' && store.getTimer.enableBreak != 'yes'"
                                            class="tt_play_btn  mr-1 tt_bod-right" @click="resumeTimer()">
                                            <i class="fa fa-play text-white"></i>
                                        </a>
                                        <a class="tt_play_btn tt_bod-right bg-danger" @click="stopTimer();"
                                            v-if="store.getTimer.trackType != 'Break'">
                                            <i class="fa fa-stop text-white"></i>
                                        </a>
                                        <a class="tt_play_btn tt_bod-right bg-danger" @click="stopBreakTime();" v-else>
                                            <i class="fa fa-stop text-white"></i>
                                        </a>
                                    </div>
                                    <div id="TimerCanves" class="DivTimer timerclck "
                                        v-if="store.getTimer.trackType == 'TimeTrack'"
                                        :class="store.$state.timerCountInSec > 0 ? 'success' : ''">
                                        {{ getTimerClock }}
                                    </div>
                                    <Timer v-else-if="store.getTimer.trackType == 'Break'"
                                        :seconds="store.$state.totalBreakRemainTime" class="timer" :store="store">
                                    </Timer>
                                    <div v-else id="TimerCanves" class="DivTimer timerclck">
                                        00:00:00
                                    </div>

                                </div>

                            </div>

                        </div>

                    </div>

                </form>
            </ValidationObserver>
        </div>

        <AddNewTags v-on:close="openTagsAdd(e)" v-if="addTagsModal" />
    </section>
</template>

<script>
// import the component
import Treeselect from '@riophae/vue-treeselect'
// import the styles
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import {
    defineComponent,
    reactive,
    ref,
    toRefs
} from '@vue/composition-api'
import {
    userTimerInfo
} from "../../stores/TimeTrack"
import {
    storeToRefs
} from 'pinia';
import moment from "moment"
import Multiselect from 'vue-multiselect'
import Timer from '@/components/TimeTrack/TimerAnimation.vue'
import {
    getSecondsFromDate,
    timeToSeconds,
    dateAndTimeZone,
    dateTimeFromat
} from "@/assets/common/vue-common.js"
import BreakModal from "../TimeTrack/modals/BreakModal.vue"
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
import AddNewTags from "./modals/AddNewTags.vue"
extend('required', {
    validate(value) {
        if (value.match(/^\s*$/)) {
            value = ''
        }
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };
    },
    message: '{_field_} is required',
    computesRequired: true
});
export default defineComponent({
    name: "Timer Add",
    components: {
        Multiselect,
        BreakModal,
        ValidationProvider,
        ValidationObserver,
        AddNewTags,
        Timer,
        Treeselect
    },
    data() {
        return {
            //open manual timer
            menualTime: false,
            breakTimer: "00:00:00",
            //check break timer on or off
            breakTimerOn: false,
            textCountertlength: 2000,
            submitted: false,
            startTimerShow: false,
            timer: "00:00:00",
            error: "",
            timerStart: false,
            timerPouse: false,
            //Check time or break on 
            timerOrBreak: false,
            breakModalPopup: false,
            formData: {},
            tagShow: false,
            tagSelect: [],
            addTagsModal: false,
            breakTimerSecond: 50 + 's',

        }
    },
    created() {
        this.getInComplete();
    },
    mounted() {
        //This script use for tags and break modal auto off when user click any other actions
        document.addEventListener("click", (e) => {
            if (this.tagShow) {
                if (e.target == this.$refs.tags || e.target.parentNode == this.$refs.tags || e.target.parentNode.closest(".tagsModals") == this.$refs.tagsModals || e.target == this.$refs.tagsModals) return
                this.tagShow = false
            }
            if (this.breakModalPopup) {
                if (e.target == this.$refs.break || e.target.parentNode == this.$refs.break || e.target == this.$refs.breakModal || e.target.parentNode.closest(".breakModal") == this.$refs.breakModal) { return }
                else {
                    this.breakModalPopup = false
                }
            }
        })
        var vm = this;
        document.addEventListener("visibilitychange", function () {
            // Check if tab content is visible
            if (document.visibilityState) {
                if (vm.store.getTimer?.statusCode) {
                    vm.trackerData = Object.assign(vm.trackerData, vm.store.getTimer)
                    //vm.trackerData.taskId =vm.trackerData.taskId==0 ? null : vm.store.getTimer.taskId
                    vm.timerOrBreak = true
                    if (vm.store.getTimer?.trackType == 'Break') {
                        vm.store.$state.breakNegative = true
                        vm.timerBreakCalculater()
                    } else {
                        vm.timerCalculater('tabChange')
                    }
                }
                //console.log("Tab is visible!");
            }


        });





    },
    unmounted() {
        this.store.$state.tasks = []
        this.store.$state.timerRunResponse = ''
        this.error = ''
    },
    methods: {
        GetPaidOrUnpaidBreak(data) {
            this.trackerData.isBillable = data.isPaid;
        },
        clearTags() {
            this.tagSelect = []
        },
        //Calculate timer 
        timerBreakCalculater() {
            let seconds = getSecondsFromDate({
                startDate: this.store.getTimer?.dateStart,
                endDate: dateAndTimeZone(moment())
            }, 'seconds')
            this.breakTimerOn = true
            //Get second from beark remain break timer
            this.store.$state.breakTimerCountInSec = timeToSeconds(this.store.getTimer.remainingBreakDuration) - seconds
            this.store.$state.timerCountInSec = parseInt(this.store.getTimer.resumeHours)
            this.store.$state.totalBreakRemainTime = this.store.$state.breakTimerCountInSec > 0 ? this.store.$state.breakTimerCountInSec : 0
            this.startBreakTime();
            this.trackerData.trackDescription = this.store.getTimer.breakName
            this.store.getTimer.projectId ? this.projectId() : this.trackerData.projectId = null
            this.trackerData.taskId = null
        },
        async timerCalculater(event) {
            let countResumeInSeconds = 0
            let formatDate = moment(new Date(this.store.getTimer?.dateStop), "DD/MM/YYYY HH:mm:ss")
            // this.store.$state.timerCountInSec = moment(new Date(this.store.getTimer?.dateStop), "DD/MM/YYYY HH:mm:ss").format("DD-MM-YYYY") == "01-01-1950" ?
            this.store.$state.timerCountInSec = moment(formatDate).format("DD-MM-YYYY") == "01-01-1950" ?
                getSecondsFromDate({
                    startDate: this.store.getTimer?.dateStart,
                    endDate: dateAndTimeZone(moment())
                }, 'seconds') : countResumeInSeconds + parseInt(this.store.getTimer.resumeHours)
            if (this.store.getTimer?.statusCode != "ACT") {
                this.store.setTimerClock(this.store.$state.timerCountInSec);
                this.timerPouse = true

            } else {
                this.store.startTimer()

            }
            this.store.getTimer.projectId ? this.projectId('tabChange') : this.trackerData.projectId = null
            if (this.store.getTimer.projectId) {
                if (event != 'tabChange') {
                    await this.store.loadTasks(this.store.getTimer.projectId);
                }
                //this.taskId()
            } else {
                this.trackerData.taskId = null
            }
        },
        //When Component render then call
        async getInComplete() {
            var vm = this;
            await vm.store.getTimerInfo()
            vm.store.getBreakTime();
            await vm.store.tagGetForTimer()
            //set blank object 
            Object.assign(vm.formData, vm.trackerData);

            await vm.store.getProjects()

            vm.store.$state.tasks = []
            vm.store.$state.timerRunResponse = ''
            vm.error = ''
            if (vm.store.getTimer?.statusCode) {
                vm.trackerData = Object.assign(vm.trackerData, vm.store.getTimer)
                //this.trackerData.taskId= this.trackerData.taskId == 0 ? null : vm.store.getTimer.taskId
                vm.timerOrBreak = true

                if (vm.trackerData.tagData) {
                    let array = vm.trackerData.tagData.split(',')
                    array.map((item) => {
                        vm.tagSelect.push(vm.store.getTags.find((data) => data.tagId == item))
                    })
                }
                if (vm.store.getTimer?.trackType == 'Break') {
                    vm.store.$state.breakNegative = true
                    vm.timerBreakCalculater()
                } else {
                    vm.timerCalculater()
                }
            }

        },
        //get task according to project id
        taskByProject() {
            // this.trackerData.taskId = ''
            this.store.$state.tasks = []
            this.trackerData.taskId = null;
            if (this.trackerData.projectId) {
                this.store.loadTasks(this.trackerData.projectId?.projectId)
            }
            if (this.store.getTimer.statusCode && (this.trackerData.trackDescription || this.trackerData.projectId)) {
                this.submit(true)
            }
            if (this.store.getTimer.statusCode && (!this.trackerData.trackDescription && !this.trackerData.projectId)) {
                this.ShowAlert(this.$t("selectProjectDesc"), 'warning', true, this.$t('Alert'));
            }


        },
        startbreak() {
            this.projectId()
            this.taskId()
        },
        //On and off Break moadal
        breakModalOpen(event) {
            if (this.store.getTimer.statusCode && (!this.trackerData.trackDescription && !this.trackerData.projectId)) {
                this.ShowAlert(this.$t("selectProjectDesc"), 'warning', true, this.$t('Alert'));
            } else {
                this.breakModalPopup = this.breakModalPopup = !this.breakModalPopup
                this.breakTimerOn = event.breakTimer
                this.timerOrBreak = this.store.$state.timerCountInSec > 0 ? true : event.breakTimer
            }

        },

        resumeTimer() {
            //  this.tagSelect.map((items, index) => {
            //      this.trackerData.TagIds += index != 0 ? ',' + items.tagId : items.tagId
            // })
            this.trackerData.startTime = dateAndTimeZone(moment(), dateTimeFromat)
            this.trackerData.stopTime = dateAndTimeZone(moment(), dateTimeFromat)
            this.submitTimerInfo('resume')
            this.timerOrBreak = true

        },
        async pouseTimer() {
            this.tagSelect.map((items, index) => {
                this.trackerData.TagIds += index != 0 ? ',' + items.tagId : items.tagId
            })
            this.trackerData.stopTime = dateAndTimeZone(moment(), dateTimeFromat)
            this.trackerData.totalHours = this.store.$state.timerCountInSec
            await this.submitTimerInfo('pause')
        },
        //Timer stop ,pouse and start Information send to backend
        async submitTimerInfo(event) {
            if (this.store.getTimer.statusCode && (!this.trackerData.trackDescription && !this.trackerData.projectId)) {
                this.ShowAlert(this.$t("selectProjectDesc"), 'warning', true, this.$t('Alert'));
            } else {
                this.trackerData.CreatedBy = this.store.getUserInformation.id
                this.trackerData.createdBy = this.store.getUserInformation.id
                this.trackerData.trackId = this.store.getTimer?.trackId
                this.trackerData.trackDescription = this.store.getTimer?.trackDescription
                this.trackerData.timerStatus = event,
                    //this.trackerData.stopBreak = moment().format('YYYY-MM-DD HH:mm:ss')
                    this.trackerData.type = "TimeTrack",
                    this.trackerData.trackType = "TimeTrack"
                this.trackerData.projectId = this.store.getTimer.projectId ? this.store.getTimer.projectId : this.trackerData.projectId == null ? 0 : this.trackerData.projectId ? this.trackerData.projectId.projectId : this.trackerData.projectId
                this.trackerData.currentDate = dateAndTimeZone(moment(), dateTimeFromat)
                this.trackerData.TagIds = ''
                this.tagSelect.map((items, index) => {
                    this.trackerData.TagIds += index != 0 ? ',' + items.tagId : items.tagId
                })
                await this.store.submitProjectInfo(this.trackerData)
                await this.store.getTimerInfo();
                this.projectId()
                this.taskId()

                if (this.store.getTimerRunResponse.userMsg == "TimerStopped") {
                    Object.assign(this.trackerData, this.formData)
                    await this.store.getTimerInfo();
                    this.$refs.form.reset()
                    this.timerOrBreak = false
                    this.ShowAlert(this.$t('TimerStopped'), "danger", true, this.$t('Alert'));
                    const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                    this.store.getTimerListing1(url)

                }
                if (this.store.getTimerRunResponse.status == 0) {
                    if (event == 'resume') {
                        this.store.startTimer()
                    }
                    else if (event == 'pause') {
                        this.store.stopTimer()
                    }
                    else if (event == 'stop' || event == 'stopresume') {
                        this.timerOrBreak = false
                        this.trackerData.projectId = 0
                        this.trackerData.taskId = null
                        this.trackerData.trackDescription = ''
                        this.store.stopTimer()
                        this.store.$state.timerCountInSec = 0
                        this.store.$state.timerClock = "00:00:00"
                        this.$refs.form.reset()
                        this.tagSelect = []
                        Object.assign(this.trackerData, this.formData)
                        const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                        this.store.getTimerListing1(url)
                        const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                        this.store.getTrackedtimings(url1)
                    }
                    //if(this.store.getTimerRunRespolnse())

                }
            }
        },
        async stopTimer() {

            this.trackerData.TagIds = ''
            if (this.trackerData.projectId || this.trackerData.trackDescription) {
                let timerstatus = this.store.getTimer.statusCode == 'PAUS' ? 'stopresume' : 'stop'
                this.trackerData.stopTime = dateAndTimeZone(moment(), dateTimeFromat)
                this.trackerData.totalHours = this.store.$state.timerCountInSec
                this.trackerData.trackId = this.store.getTimer.trackId
                this.tagSelect.map((items, index) => {
                    this.trackerData.TagIds += index != 0 ? ',' + items.tagId : items.tagId
                })
                await this.submitTimerInfo(timerstatus)
                this.store.$state.tasks = []
            } else {
                this.ShowAlert(this.$t("selectProjectDesc"), 'warning', true, this.$t('Alert'));
            }

        },
        startBreakTime() {
            //this.breakTimerShow = true
            this.store.breakStartTimer()
        },
        //Stop Break
        async stopBreakTime() {
            this.trackerData.CreatedBy = this.store.getUserInformation.id
            this.trackerData.trackId = this.store.getTimer.trackId
            this.trackerData.trackDescription = this.store.getTimer?.trackDescription
            this.trackerData.timerStatus = "stop",
                this.trackerData.type = "Break"
            this.trackerData.BreakStartTime = this.store.getTimer.dateStart
            this.trackerData.createdBy = this.store.getUserInformation.id
            this.trackerData.BreakStopTime = dateAndTimeZone(moment(), dateTimeFromat)
            this.trackerData.BreakId = this.store.getTimer.breakId
            this.trackerData.breakId = this.store.getTimer.breakId
            this.trackerData.currentDate = dateAndTimeZone(moment(), dateTimeFromat)
            let seconds = getSecondsFromDate({
                startDate: this.store.getTimer?.dateStart,
                endDate: dateAndTimeZone(moment())
            }, 'seconds')
            this.trackerData.totalHours = seconds
            this.trackerData.projectId = this.trackerData.projectId ? this.trackerData.projectId.projectId : this.trackerData.projectId
            //this.trackerData.taskId = this.trackerData.taskId ? this.trackerData.taskId : this.trackerData.taskId
            await this.store.submitProjectInfo(this.trackerData)
            if (this.store.getTimerRunResponse.status == 0) {
                this.ShowAlert(this.$t('BreakEntryStopSuccessfully'), "success", true, this.$t('Alert'));
                this.store.breakStopTimer()
                this.breakTimerOn = false
                this.store.breakSetTimerClock(0)
                this.store.$state.timerRunResponse = ''
                this.store.$state.breakTimerCountInSec = 0
                this.timerOrBreak = false
                Object.assign(this.trackerData, this.formData);
                await this.store.getTimerInfo();
                const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                if (this.store.getTimer.statusCode == "PAUS" && this.store.getTimer.trackType == 'TimeTrack') {
                    Object.assign(this.trackerData, this.store.getTimer)
                    this.trackerData.TagIds = this.store.getTimer.tagData
                    this.store.$state.timerCountInSec = parseInt(this.store.getTimer.resumeHours)
                    this.trackerData.isBillable = this.store.getTimer.isBillable

                    setTimeout(() => {
                        this.resumeTimer()
                        this.store.getTimerListing1(url)
                        this.store.getTrackedtimings(url1)
                    }, 1000)


                } else {
                    this.store.getTimerListing1(url)
                    this.store.getTrackedtimings(url1)
                }



                this.projectId()
                this.taskId()
            }



        },
        //Submit Add Timer Form
        async submit(e) {
            if (e) {
                this.trackerData.timerStatus = "update"
                this.trackerData.trackId = this.store.getTimer.trackId
            }
            this.submitted = true
            this.trackerData.projectId = this.trackerData?.projectId ? this.trackerData?.projectId?.projectId : 0
            //this.trackerData.taskId = this.trackerData?.taskId ? this.trackerData?.taskId : 0
            this.trackerData.isBillable = this.trackerData?.isBillable ? 1 : 0
            this.trackerData.timeformat = this.trackerData?.format ? 24 : 12
            this.trackerData.stopBreak = null
            this.trackerData.createdBy = this.store.getUserInformation.id
            this.trackerData.TagIds = ''
            this.trackerData.startTime = dateAndTimeZone(moment(), dateTimeFromat)
            this.trackerData.stopTime = dateAndTimeZone(moment(), dateTimeFromat)
            this.trackerData.currentDate = dateAndTimeZone(moment(), dateTimeFromat)
            this.tagSelect.map((items, index) => {
                this.trackerData.TagIds += index != 0 ? ',' + items.tagId : items.tagId
            })
            await this.store.submitProjectInfo(this.trackerData);
            //this.trackerData.taskId = this.trackerData?.taskId == 0  ? null : this.trackerData?.taskId 
            this.startTimerShow = this.startTimerShow;

            if (this.store.getTimerRunResponse?.trackId) {
                await this.store.getTimerInfo();
                if (e != true) {
                    //this.tagSelect = []
                    this.store.startTimer(0)

                }
                this.submitted = false
                this.timerOrBreak = true
            } else {
                this.error = this.store.getTimerRunResponse?.userMsg;
            }

            this.projectId()
            this.taskId()

        },
        descriptionChange() {
            if (this.trackerData.trackDescription.match(/^\s*$/)) {
                this.trackerData.trackDescription = ''
            }

            if (this.store.getTimer.statusCode && (this.trackerData.trackDescription || this.trackerData.projectId)) {
                if (this.trackerData.trackDescription != this.store.getTimer.trackDescription) {
                    this.submit(true)
                }

            }
        },
        //If project id get number from backend then set custom object for multydropdown
        async projectId(e) {
            this.trackerData.projectId = this.store.getTimer.projectId ? this.store.getProjectsByUser.find((data) => data.projectId == this.store.getTimer.projectId) : null
            this.store.getTimer.projectId && e != 'tabChange' ? await this.store.loadTasks(this.store.getTimer.projectId) : ''
        },
        //If task id get number from backend then set custom object for multydropdown
        taskId() {
            // this.trackerData.taskId = this.store.getTimer.taskId ? this.store.getTasksFromProject.find((data) => data.value == this.store.getTimer.taskId) : null
        },
        //Open Add tags modal
        openTagsAdd(e) {
            this.addTagsModal = e
        },
        //Get isBillable status from task  is
        isBillableSatus() {
            // this.trackerData.isBillable = this.trackerData.taskId ? this.trackerData.taskId.isBillable : this.trackerData.isBillable
            //console.log("getTempTasksFromProject",this.search(this.getTempTasksFromProject,this.trackerData.taskId,'value').isBillable)
            this.trackerData.isBillable = this.search(this.getTempTasksFromProject, this.trackerData.taskId, 'value') ? this.search(this.getTempTasksFromProject, this.trackerData.taskId, 'value').isBillable : this.trackerData.isBillable;

            if (this.store.getTimer.statusCode) {
                this.submit(true)
            }
            // console.log("checkk",this.trackerData.taskId.isBillable)
        },
        search(tree, value, key = 'id', reverse = false) {
            // Make a copy of the tree array to avoid modifying the original array
            const stack = [...tree];

            // Continue searching while there are nodes in the stack
            while (stack.length) {
                // Retrieve the next node (pop from end if reverse is true, shift from start otherwise)
                const node = stack[reverse ? 'pop' : 'shift']();

                // Check if the current node matches the search criteria
                if (node[key] === value) {
                    return node; // Return the found node
                }

                // If the current node has children, add them to the stack
                if (node.children) {
                    stack.push(...node.children);
                }
            }

            // Return null if no matching node is found
            return null;
        },
        //When open tags select box then auto show select box options
        tagOpenModal() {
            setTimeout(() => {
                this.$refs.tagsMultiselect.activate()
            }, 1)

        },

    },
    setup() {
        const store = userTimerInfo();
        const {
            getProjectsByUser,
            getTasksFromProject,
            getLoadingStatus,
            getUserInformation,
            getTimerRunResponse,
            getTimerClock,
            getTempTasksFromProject = getTasksFromProject,
            getTimer
        } = storeToRefs(store);
        const trackerData = reactive({
            projectId: null,
            trackDescription: "",
            taskId: null,
            isBillable: true,
            format: false,
            date: null,
            trackId: 0,
            startTime: dateAndTimeZone(moment(), dateTimeFromat),
            stopTime: dateAndTimeZone(moment(), dateTimeFromat),
            companyId: null,
            currentDate: dateAndTimeZone(moment(), dateTimeFromat),
            trackAgent: "web",
            timerStatus: "start",
            CreatedBy: getUserInformation.value.id,
            createdBy: getUserInformation.value.id,
            linkedTrackId: null,
            totalHours: 0,
            timeformat: "12",
            TagIds: '',
            isForcedUpdate: null,
            type: "TimeTrack",
            BreakDate: dateAndTimeZone(moment(), dateTimeFromat),
            BreakName: null,
            BreakId: 0,
            BreakStartTime: 0,
            Shift_ID: 0,
            SelectedDate: "",
            Break_Shift: "",
            BreakStopTime: dateAndTimeZone(moment(), dateTimeFromat)
        })

        //  console.log("projects",projects.value)


        // Description text length counter
        function textCounter(e) {
            if (e) {
                if ($.inArray(e.keyCode, [46, 8, 9, 27, 13]) !== -1 ||
                    // Allow: Ctrl+A, Command+A
                    (e.keyCode === 65 && (e.ctrlKey === true || e.metaKey === true)) ||
                    // Allow: home, end, left, right, down, up
                    (e.keyCode >= 35 && e.keyCode <= 40)) {
                    // let it happen, don't do anything
                    return;
                }
                // Ensure that it is a number and stop the keypress
                if ((e.key == "<" || (e.key == ">"))) {
                    e.preventDefault();
                }
            }
        }

        return {
            trackerData,
            getTimerClock,
            getProjectsByUser,
            textCounter,
            getTasksFromProject,
            getLoadingStatus,
            getUserInformation,
            getTimerRunResponse,
            store,
            getTempTasksFromProject
        }
    },
})
</script>

